import React, { useEffect, useState } from 'react'
import { Container, Row, Col } from 'reactstrap'
import { useDispatch, useSelector } from 'react-redux'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import './Proposals.scss'
import {
  GenericSearchBar,
  PageHelmet,
  // CircularProgress,
  Table,
} from 'components'
import { searchDataUpdater } from 'store/header/thunks'
import SwiperCore, { A11y, Navigation, Pagination, Scrollbar } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react'
// import 'swiper/components/navigation/navigation.scss'
// import 'swiper/components/pagination/pagination.scss'
// import 'swiper/swiper.scss'
import { withTranslation } from 'react-i18next'
import { pageChange } from 'store/pagination/thunks'
import endpoints from '../../config/urlconfigs'
import { Amount } from '../../config/config'
import request from '../../api/request'
import moment from 'moment'
import Loader from 'components/Loader/Loader'
import { PieChart } from 'react-minimal-pie-chart'

var bigDecimal = require('js-big-decimal')
SwiperCore.use([Navigation, Pagination, Scrollbar, A11y])

const Proposals: React.FC = (props: any) => {
  // All states for current screen
  const dispatch = useDispatch(),
    [showRecord, setTotalRecords] = useState<any>(20),
    [currentPage, setCurrentPage] = useState(1),
    [pageCount, setPageCount] = useState(0),
    [data, setData] = useState<any>([]),
    [topData, setTopData] = useState<any>([]),
    [CSVData, setCSVData] = useState([]),
    [loading, setLoading] = useState(false)

  const searchedData = useSelector((state: any) => state.header.searchedData)
  const pagination = useSelector((state: any) => state.pagination)

  const getPretty = (val: any) => {
    var value = bigDecimal.getPrettyValue(val)
    return value
  }

  let numberIndex = 0
  let numberIndex2 = 0

  numberIndex = showRecord * (currentPage === 0 ? 0 : currentPage - 1)
  numberIndex2 = showRecord * (currentPage === 0 ? 0 : currentPage - 1)

  useEffect(() => {
    request('GET', `${endpoints.proposals}`, {
      pageSize: showRecord,
      page: currentPage,
    })
      .then((res: any) => {
        setTopData(res.data)
      })
      .catch((err: any) => {
        return err
      })
  }, [])

  useEffect(() => {
    getProposals()
  }, [showRecord, currentPage])

  // Get proposals API call
  const getProposals = () => {
    setLoading(true)
    request('GET', `${endpoints.proposals}`, {
      pageSize: showRecord,
      page: currentPage,
    })
      .then((res: any) => {
        setData(res.data)
        let CSVData: any = []
        setCSVData([])
        for (let i = 0; i < res.data.length; i++) {
          numberIndex2 = numberIndex2 + 1
          CSVData.push({
            No: numberIndex2,
            ProposalID: res.data[i].proposal_id,
            Title: res.data[i].proposal_title,
            Proposer: res.data[i].proposer_name,
            ProposalType: res.data[i].proposal_type,
            Amount: getPretty(Amount(res.data[i].fund_amount)) + ' BOA',
            ProposalDate: props.i18n.t('Height') + ": " + getPretty(res.data[i].block_height) + " / " + moment.utc(res.data[i].proposal_date * 1000).format('YYYY-MM-DD HH:mm:ss') + " UTC",
            VotingStartHeight: props.i18n.t('Height') + ": " + getPretty(res.data[i].vote_start_height) + " / " + moment.utc(res.data[i].voting_start_date * 1000).format('YYYY-MM-DD HH:mm:ss') + " UTC",
            VotingEndHeight: props.i18n.t('Height') + ": " + getPretty(res.data[i].vote_end_height) + " / " + moment.utc(res.data[i].voting_end_date * 1000).format('YYYY-MM-DD HH:mm:ss') + " UTC",
            Status: res.data[i].proposal_status,
          })
        }
        setCSVData(CSVData)
        setPageCount(res.data[0].full_count)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        return err
      })
  }
  // Show number of records in list function
  const numberOfRecordShow = (Record: Number) => {
    setTotalRecords(Record)
    setCurrentPage(1)
  }

  useEffect(() => {
    setCurrentPage(pagination.pageNumber)
  }, [pagination.pageNumber])

  // Redirect to other screen functions
  const proposalDetails = (proposalID: any) => {
    props.history.push(`${RouterPathEnum.PROPOSALDETAILS}/${proposalID}`)
  }

  const blockDetails = (height: string) => {
    props.history.push(`${RouterPathEnum.BLOCKSDETAILS}/height:${height}`)
  }

  // Slider settings
  const HorizontalSwiperParams = {
    pagination: { clickable: true, dynamicBullets: true },
    breakpoints: {
      1024: {
        slidesPerView: 2.5,
      },
      991: {
        slidesPerView: 2.5,
      },
      767: {
        slidesPerView: 1.5,
      },
      640: {
        slidesPerView: 1.5,
      },
      320: {
        slidesPerView: 1.1,
      },
    },
    navigation: {
      nextEl: '#js-next1',
      prevEl: '#js-prev1',
    },
    slidesPerView: 2.5,
  }

  return (
    <div className="page-container">
      <div id="proposals">
        <PageHelmet
          title={`BOSAGORA Explorer - ${props.title}`}
          meta={{ name: 'Proposals', content: 'BOSAGORA Explorer - Proposals' }}
        />
        <Container fluid="xl">
          <Row>
            <Col lg={12} md={12} sm={12}>
              {/* Proposals Top section  */}
              <div className="top-header2">
                <div className="left"></div>
                <div className="right">
                  <GenericSearchBar
                    history={props.history}
                    searchedDataGet={(searchid: any) =>
                      dispatch(searchDataUpdater(searchid))
                    }
                    searchedData={searchedData}
                  />
                </div>
              </div>
            </Col>
          </Row>
          <Row>
            <Col lg={12} md={12} sm={12}>
              <h2>{props.i18n.t('Proposals')}</h2>
            </Col>
          </Row>
          {/* Proposals Slides */}
          {loading ? (
            <div className="no-data">
              <Loader />
            </div>
          ) : topData.length === 0 ? (
            <div className="no-data">
              <h4>{props.i18n.t('NoRecord')}</h4>
            </div>
          ) : (
            <Swiper {...HorizontalSwiperParams}>
              {topData &&
                topData.slice(0, 9).map((data: any, index: number) => {
                  return (
                    <SwiperSlide key={index}>
                      <div className="proposal-Cont">
                        <div className="proposal-box" onClick={() => proposalDetails(data.proposal_id)}>
                          <div className="bt-header">
                            <h5>
                              {' '}
                              {props.i18n.t('Proposal_ID')} #{data.proposal_id}{' '}
                            </h5>
                            <span>
                              {props.i18n.t(data.proposal_status)}
                            </span>
                          </div>
                          <h5 className="proposal-heading">
                            {data.proposal_title}
                          </h5>
                          <div className="proposal-gift-box">
                            <div className="gift-box-inner">
                              <div className="gift-cont">
                                <div>{props.i18n.t('Proposer')}</div>
                                <div className="black">
                                  {data.proposer_name}
                                </div>
                              </div>
                              <div className="gift-cont">
                                <div>{props.i18n.t('Voting_Start')}</div>
                                <div>
                                  <p className="black mb-0">
                                    {props.i18n.t('Height')}:{' '}
                                    {getPretty(data.vote_start_height)}
                                  </p>
                                  <p>
                                    {moment.utc(
                                      data.voting_start_date * 1000,
                                    ).format('YYYY-MM-DD HH:mm:ss')} UTC
                                  </p>
                                </div>
                              </div>
                              <div className="gift-cont">
                                <div>{props.i18n.t('Voting_End')}</div>
                                <div>
                                  <p className="black mb-0">
                                    {props.i18n.t('Height')}:{' '}
                                    {getPretty(data.vote_end_height)}
                                  </p>
                                  <p>
                                    {props.i18n.t('Expect')}{' '}
                                    {moment.utc(data.voting_end_date * 1000).format(
                                      'YYYY-MM-DD HH:mm:ss',
                                    )} UTC
                                  </p>
                                </div>
                              </div>
                              {data.proposal_status === 'Closed' && (
                                <div className="gift-cont">
                                  <div>{props.i18n.t('Proposal_Result')}</div>
                                  <div className="black">
                                    {props.i18n.t(data.proposal_result)}
                                  </div>
                                </div>
                              )}
                              {(data.proposal_status === 'Ongoing' ||
                                data.proposal_status === 'CountingVotes') && (
                                  <div className="gift-cont">
                                    <div>{props.i18n.t('Total_Validators')}</div>
                                    <div className="black">
                                      {data.total_validators}
                                    </div>
                                  </div>
                                )}
                            </div>
                            {data.proposal_status === 'Voting' && (
                              <div className="graph-box">
                                <div className="PieChartCustom">
                                  <div className="inner">
                                    <div>
                                      <span
                                        className={
                                          Math.max(
                                            parseFloat(data.voted_percentage),
                                            parseFloat(data.not_voted_percentage),
                                          ) === parseFloat(data.voted_percentage)
                                            ? 'green-text'
                                            : 'mehroon-text'
                                        }
                                      >
                                        {Math.max(
                                          parseFloat(data.voted_percentage),
                                          parseFloat(data.not_voted_percentage),
                                        ).toFixed() + '%'}
                                      </span>
                                      <br />
                                      {props.i18n.t('Most_Voted_On')}
                                    </div>
                                  </div>
                                  <PieChart
                                    lineWidth={15}
                                    viewBoxSize={[100, 100]}
                                    data={[
                                      {
                                        title: props.i18n.t('Voted'),
                                        value: parseFloat(data.voted_percentage),
                                        color: '#5bbd60',
                                      },
                                      {
                                        title: props.i18n.t('Not_Voted'),
                                        value: parseFloat(data.not_voted_percentage),
                                        color: '#707070',
                                      },
                                    ]}
                                  />
                                </div>
                              </div>
                            )}
                            {data.proposal_status === 'Closed' && (
                              <div className="graph-box">
                                <div className="PieChartCustom">
                                  <div className="inner">
                                    <div>
                                      <span
                                        className={
                                          Math.max(
                                            parseFloat(data.yes_percentage),
                                            parseFloat(data.no_percentage),
                                            parseFloat(data.abstain_percentage),
                                            parseFloat(data.not_voted_percentage),
                                          ) === parseFloat(data.yes_percentage)
                                            ? 'green-text'
                                            : Math.max(
                                              parseFloat(data.yes_percentage),
                                              parseFloat(data.no_percentage),
                                              parseFloat(data.abstain_percentage),
                                              parseFloat(data.not_voted_percentage),
                                            ) === parseFloat(data.no_percentage)
                                              ? 'no-text'
                                              : Math.max(
                                                parseFloat(data.yes_percentage),
                                                parseFloat(data.no_percentage),
                                                parseFloat(data.abstain_percentage),
                                                parseFloat(data.not_voted_percentage),
                                              ) === parseFloat(data.abstain_percentage)
                                                ? 'yellow-text'
                                                : 'mehroon-text'
                                        }
                                      >
                                        {Math.max(
                                          parseFloat(data.yes_percentage),
                                          parseFloat(data.no_percentage),
                                          parseFloat(data.abstain_percentage),
                                          parseFloat(data.not_voted_percentage),
                                        ).toFixed() + '%'}
                                      </span>
                                      <br />
                                      {props.i18n.t('Most_Voted_On')}
                                    </div>
                                  </div>
                                  <PieChart
                                    lineWidth={15}
                                    viewBoxSize={[100, 100]}
                                    data={[
                                      {
                                        title: props.i18n.t('Yes'),
                                        value: parseFloat(data.yes_percentage),
                                        color: '#5bbd60',
                                      },
                                      {
                                        title: props.i18n.t('No'),
                                        value: parseFloat(data.no_percentage),
                                        color: '#e15859',
                                      },
                                      {
                                        title: props.i18n.t('Abstain'),
                                        value: parseFloat(data.abstain_percentage),
                                        color: '#343a40',
                                      },
                                      {
                                        title: props.i18n.t('Not_Voted'),
                                        value: parseFloat(data.not_voted_percentage),
                                        color: '#707070',
                                      },
                                    ]}
                                  />
                                </div>
                              </div>
                            )}
                          </div>
                          {data.proposal_status === 'Closed' && (
                            <ul className="proposal-list list-unstyled m-0">
                              <li>
                                <span className="green-check"></span>{' '}
                                {props.i18n.t('Yes')}{' '}
                                {parseFloat(data.yes_percentage).toFixed(0)}%
                              </li>
                              <li>
                                <span className="red-check"></span>{' '}
                                {props.i18n.t('No')}{' '}
                                {parseFloat(data.no_percentage).toFixed(0)}%
                              </li>
                              <li>
                                <span className="black-check"></span>{' '}
                                {props.i18n.t('Abstain')}{' '}
                                {parseFloat(data.abstain_percentage).toFixed(0)}%
                              </li>
                              <li>
                                <span className="grey-check"></span>{' '}
                                {props.i18n.t('Not_Voted')}{' '}
                                {parseFloat(data.not_voted_percentage).toFixed(0)}%
                              </li>
                            </ul>
                          )}
                          {data.proposal_status === 'Voting' && (
                            <ul className="proposal-list list-unstyled">
                              <li>
                                <span className="green-check"></span>{' '}
                                {props.i18n.t('Voted')}{' '}
                                {parseFloat(data.voted_percentage).toFixed(0)}%
                              </li>
                              <li>
                                <span className="grey-check"></span>{' '}
                                {props.i18n.t('Not_Voted')}{' '}
                                {parseFloat(data.not_voted_percentage).toFixed(0)}%
                              </li>
                            </ul>
                          )}
                        </div>
                      </div>
                    </SwiperSlide>
                  )
                })}
            </Swiper>
          )}
          {/* Proposals list */}
          <div className="proposals">
            <h2>{props.i18n.t('Proposal_List')}</h2>
            <Table
              headerData={[
                {
                  No: '',
                  Proposal_ID: '',
                  Title: '',
                  Proposer: '',
                  Proposal_Type: '',
                  Amount: '',
                  Proposal_Date: '',
                  Voting_Start_Height: '',
                  Voting_End_Height: '',
                  Status: '',
                },
              ]}
              headData={
                data &&
                data.map((proposal: any, index: number) => {
                  numberIndex = numberIndex + 1
                  return {
                    No: numberIndex,
                    Proposal_ID: (
                      <div
                        className="p-title link-color"
                        onClick={() => proposalDetails(proposal.proposal_id)}
                      >
                        {proposal.proposal_id}
                      </div>
                    ),
                    Title: (
                      <div
                        className="p-title link-color"
                        onClick={() => proposalDetails(proposal.proposal_id)}
                      >
                        <span>{proposal.proposal_title}</span>
                      </div>
                    ),
                    Proposer: proposal.proposer_name,
                    Proposal_Type: props.i18n.t(proposal.proposal_type),
                    Amount: getPretty(Amount(proposal.fund_amount)) + ' BOA',
                    Proposal_Date: (
                      <div className="start_height">
                        {/* {props.i18n.t('Height')}:  */}
                        <div className="link-color" onClick={() => blockDetails(proposal.block_height)}> {getPretty(proposal.block_height)}</div>
                        {/* / {moment.utc(proposal.proposal_date * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC */}
                      </div>
                    ),
                    Voting_Start_Height: (
                      <div className="start_height">
                        {/* {props.i18n.t('Height')}:  */}
                        <div className="link-color" onClick={() => blockDetails(proposal.vote_start_height)}> {getPretty(proposal.vote_start_height)}</div>
                        {/* / {moment.utc(proposal.voting_start_date * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC */}
                      </div>
                    ),
                    Voting_End_Height: (
                      <div className="start_height">
                        {/* {props.i18n.t('Height')}:  */}
                        <div className="link-color" onClick={() => blockDetails(proposal.vote_end_height)}> {getPretty(proposal.vote_end_height)}</div>
                        {/* / {moment.utc(proposal.voting_end_date * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC */}
                      </div>
                    ),
                    Status: props.i18n.t(proposal.proposal_status),
                  }
                })
              }
              loading={loading}
              currentPage={currentPage}
              pageCount={pageCount}
              pageChange={(pageNumber: number) =>
                dispatch(pageChange(pageNumber))
              }
              showRecord={showRecord}
              props={props}
              type="Proposals"
              CSVData={CSVData}
              data={data}
              fileName={'Proposals List.csv'}
              numberOfRecordShow={(Record: number) =>
                numberOfRecordShow(Record)
              }
            />
          </div>
        </Container>
      </div>
    </div>
  )
}

export default withTranslation()(Proposals)
