import React, { useState, useEffect } from 'react'
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Tooltip,
  Row,
  Col,
} from 'reactstrap'
import classnames from 'classnames'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import { useDispatch, useSelector } from 'react-redux'
import { ReactSVG } from 'react-svg'
import { pageChange } from '../../../store/pagination/thunks'
import { GoArrowRight } from 'react-icons/go'
import { MdFileDownload } from 'react-icons/md'
import blueBlock from '../../../images/blueblock.svg'
import redBlock from '../../../images/redblock.svg'
import {useTranslation, withTranslation} from 'react-i18next'
import CopyAddressClipboard from 'components/CopyAddressClipboard/CopyAddressClipboard'
import Loader from 'components/Loader/Loader'
import request from '../../../api/request'
import endpoints from '../../../config/urlconfigs'
import caretIco from '../../../images/show-caret.svg'
import moment from 'moment'
import { CSVLink } from 'react-csv'
import { Amount } from 'config/config'
import Pagination from 'react-js-pagination'
import { getPretty, getPriceOfFee} from "../../../utils/CalcUtils";
import {coinMarketCap} from "../../../store/header/thunks";

interface MarketCapStats {
  last_updated_at: any
  market_cap: number
  change_24h: number
  price: number
  time: any
  vol_24h: number
}
const ValidatorTransactionTabs: React.FC<any> = (props: any) => {

  const dispatch = useDispatch();
  const { i18n } = useTranslation();
  const pagination = useSelector((state: any) => state.pagination);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageCount, setPageCount] = useState(0);
  const [loading, setLoading] = useState(false);
  const [CSVData, setCSVData] = useState([]);
  const [showRecord, setTotalRecords] = useState<any>(20);
  const [data, setData] = useState<any>([]);
  const [validatorData, setValidatorData] = useState<any>([]);
  const [missedBlocksData, setMissedBlocksData] = useState<any>([]);
  const [calculateUptime, setCalculateUptime] = useState<any>(0);
  const [error, setError] = useState(false);
  const [activeTab, setActiveTab] = useState('1');

  const marketCapStats: MarketCapStats = useSelector(
      (state: any) => state.header.marketCapStats,
  )

  useEffect(()=>{
    dispatch(coinMarketCap(i18n.language))
  },[dispatch]);

  useEffect(()=>{
    console.log("");
    dispatch(coinMarketCap(i18n.language))
  },[i18n.language])


  useEffect(() => {
    setCurrentPage(pagination.pageNumber)
  }, [pagination.pageNumber])

  useEffect(() => {
    if (activeTab === '2') {
      getNodes()
    }
  }, [activeTab])

  useEffect(() => {
    setLoading(true)
    setError(false)
    request('GET', `${endpoints.missedBlocks}/${props.address}`, {})
      .then((res: any) => {
        let calculateUptime = 0
        setCalculateUptime(0)
        for (let i = 0; i < res.data.length; i++) {
          if (res.data[i].signed === 1) {
            calculateUptime = calculateUptime + 1
          }
        }
        calculateUptime = (calculateUptime / res.data.length) * 100
        setCalculateUptime(calculateUptime)
        setMissedBlocksData(res.data)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        return err
      })

    request('GET', `${endpoints.validatorDetails}/${props.address}`, {})
      .then((res: any) => {
        setValidatorData(res.data)
        setLoading(false)
      })
      .catch((err: any) => {
        setActiveTab('2')
        setError(true)
        setLoading(false)
        return err
      })
  }, [props.address])

  useEffect(() => {
    getNodes()
  }, [showRecord, currentPage, props.address])

  // Get latest blocks API call
  const getNodes = () => {
    setLoading(true)
    request('GET', `${endpoints.walletTransactionsHistory}/${props.address}`, {
      pageSize: showRecord,
      page: currentPage,
    })
      .then((res: any) => {
        setData(res.data)
        let CSVData: any = []
        setCSVData([])
        for (let i = 0; i < res.data.length; i++) {
          if (window.innerWidth <= 480) {
            CSVData.push({
              Hash: res.data[i].tx_hash,
              Input: res.data[i].address,
              Timestamp: moment.utc(res.data[i].time * 1000).format('YYYY-MM-DD HH:mm:ss') + " UTC",
              Output: res.data[i].peer,
              OutputAmount:
                getPretty(Amount(Math.abs(res.data[i].amount))) + ' BOA',
              OutputTotalAmount:
                getPretty(Amount(Math.abs(res.data[i].amount))) + ' BOA',
              Fee: getPretty(Amount(res.data[i].tx_fee)) + ' BOA',
              Bytes: getPretty(res.data[i].tx_size) + ' Bytes',
            })
          } else {
            CSVData.push({
              Hash: res.data[i].tx_hash,
              Input: res.data[i].address,
              Type: res.data[i].tx_type,
              Fee: getPretty(Amount(res.data[i].tx_fee)) + ' BOA',
              Bytes: getPretty(res.data[i].tx_size) + ' Bytes',
              Timestamp: moment.utc(res.data[i].time * 1000).format('YYYY-MM-DD HH:mm:ss') + " UTC",
              Output: res.data[i].peer,
              OutputAmount:
                getPretty(Amount(Math.abs(res.data[i].amount))) + ' BOA',
              OutputTotalAmount:
                getPretty(Amount(Math.abs(res.data[i].amount))) + ' BOA',
            })
          }
        }
        setCSVData(CSVData)
        setPageCount(res.data[0].full_count)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        return err
      })
  }

  const numberOfRecordShow = (Record: Number) => {
    setTotalRecords(Record)
    setCurrentPage(1)
  }

  const toggle = (tab: any) => {
    setActiveTab(tab)
  }

  const handleChangePageNumber = (e: any, page: number) => {
    dispatch(pageChange(page))
  }

  const transactionOverview = (hash: string) => {
    props.props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${hash}`)
  }

  const transactionOverviewUTXOKey = (utxoKey: string) => {
    props.props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${utxoKey}`, { utxoKey: true })
  }

  const blockDetails = (height: string, destination: string) => {
    if (destination === "validatorsSection") {
      props.props.history.push(`${RouterPathEnum.BLOCKSDETAILS}/height:${height}`, { validatorsSection: true });
    }
    else {
      props.props.history.push(`${RouterPathEnum.BLOCKSDETAILS}/height:${height}`, { enrolledValidatorsSection: true });
    }
  }

  const nodeDetails = (address: string) => {
    props.props.history.push(`${RouterPathEnum.NODEDETAILS}/${address}`)
  }

  const TooltipItem = (props: any) => {
    const { item, id } = props;

    const [tooltipOpen, setTooltipOpen] = useState(false);
    const toggle = () => setTooltipOpen(!tooltipOpen);

    useEffect(() => {
      if(activeTab === '1'){
        if(id === 0 || id === 99){
          setTimeout(() => {
            setTooltipOpen(true);
          },500)
        }
      }
    },[])


    return (
      <div className="item img-hover-zoom">
        <span onClick={() => blockDetails(item.block_height, "validatorsSection")} id={'Tooltip-' + id}>
          {item.signed === 0 ? (
            <ReactSVG src={redBlock} />
          ) : (
            <ReactSVG src={blueBlock} />
          )}
        </span>
        {id === 0 || id === 99 ? (
          <Tooltip
            placement={id===0?"top":"bottom"}
            isOpen={tooltipOpen}
            target={'Tooltip-' + id}
            autohide={false}
          >
            {getPretty(item.block_height)}
          </Tooltip>
        ) : (
          <Tooltip
            placement="top"
            isOpen={tooltipOpen}
            target={'Tooltip-' + id}
            toggle={toggle}
          >
            {getPretty(item.block_height)}
          </Tooltip>
        )}


      </div>
    )
  }

  return (
    <div className="validator-transactions-tabs">
      {/* Tabs Header part */}
      <Nav tabs>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '1' })}
            onClick={() => {
              toggle('1')
            }}
            disabled={error}
          >
            {props.i18n.t('Validator')}
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: activeTab === '2' })}
            onClick={() => {
              toggle('2')
            }}
          >
            {props.i18n.t('Transactions')}
          </NavLink>
        </NavItem>
      </Nav>
      {/* Tabs Content part */}
      <TabContent activeTab={activeTab}>
        <TabPane tabId="1">
          <div className="transaction-tab">
            <Row>
              {/* Validator Details section  */}
              <Col xl={6} lg={6} md={12}>
                {validatorData.map((item: any, i: any) => (
                  <div className="validator-detail" key={i}>
                    <h2>{props.i18n.t('Validator_Details')}</h2>
                    <div className="item" key={i}>
                      <p>{props.i18n.t('Agora_Version')}</p>
                      <div className="values">{item.agora_version}</div>
                    </div>
                    <div className="item">
                      <p>{props.i18n.t('Freezing_for_validator_Amount')}</p>
                      <div className="values">
                        {item.stake_amount
                          ? getPretty(Amount(item.stake_amount))
                          : getPretty(item.stake_amount)}{' '}
                        BOA
                      </div>
                    </div>
                    <div className="item">
                      <p>{props.i18n.t('UTXO_Key')}</p>
                      <div className="values">
                        <div className="copy-address">
                          <div onClick={() => transactionOverviewUTXOKey(item.stake)} className="fileName link-color">
                            {item.stake.slice(0, 6) +
                              `…` +
                              item.stake.slice(-1 * 6)}
                          </div>
                          <CopyAddressClipboard
                            id={item.stake}
                            text={item.stake}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="item">
                      <p>{props.i18n.t('Uptime')}</p>
                      <div className="values">{calculateUptime.toFixed()}%</div>
                    </div>
                    <div className="item">
                      <p>{props.i18n.t('Enrolled_Height')}</p>
                      <div className="values">
                        <div className="copy-address">
                          <span
                            onClick={() => blockDetails(item.enrolled_at, "enrolledValidatorsSection")}
                            className="link-color"
                          >
                            {getPretty(item.enrolled_at)}
                          </span>
                          {/* <CopyAddressClipboard id={item.enrolled_at} text={item.enrolled_at} /> */}
                        </div>
                      </div>
                    </div>
                    <div className="item">
                      <p>{props.i18n.t('Node_Name')}</p>
                      <div className="values">
                        <div className="copy-address no_cursor">
                          <div className="fileName">
                            {props.address.slice(0, 6) +
                              `…` +
                              props.address.slice(-1 * 6)}
                          </div>
                          <CopyAddressClipboard
                            id={props.address}
                            text={props.address}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="item">
                      <p>{props.i18n.t('Preimage_Height')}</p>
                      <div className="values">
                        {getPretty(item.preimage.height)}
                      </div>
                    </div>
                    <div className="item">
                      <p>{props.i18n.t('Preimage_Hash')}</p>
                      <div className="values">
                        <div className="copy-address">
                          <div className="fileName">
                            {item.preimage.hash.slice(0, 6) +
                              `…` +
                              item.preimage.hash.slice(-1 * 6)}
                          </div>
                          <CopyAddressClipboard
                            id={item.preimage.hash}
                            text={item.preimage.hash}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </Col>
              {/* Missed Blocks section */}
              <Col xl={6} lg={6} md={12}>
                <div className="mb-detail">
                  <div className="mb-header">
                    <h2>{props.i18n.t('Missed_Blocks')}</h2>
                    <p className="mb-aside">{props.i18n.t('Last_100_Blocks')}</p>
                  </div>
                  <div className="missed-block">
                    {missedBlocksData.map((block: any, index: any) => {
                      return <TooltipItem key={index} item={block} id={index} />
                    })}
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        </TabPane>
        {/* Transactions tab section  */}
        <TabPane tabId="2">
          <div className="transaction-tab">
            <h2>{props.i18n.t('Transactions')}</h2>
            <div className="transactions">
              {loading ? (
                <div className="no-data">
                  <Loader />
                </div>
              ) : data.length === 0 ? (
                <div className="no-data">
                  <h4>{props.i18n.t('NoRecord')}</h4>
                </div>
              ) : (
                <>
                  {/* Input/Output Transaction Mobile Responsive View  */}
                  {data.map((item: any, index: any) => (
                    <div className="io-res" key={index}>
                      <div className="more-detail">
                        <div className="item">
                          <p>{props.i18n.t('Hash')}</p>
                          <div className="values">
                            <div className="copy-address link-color">
                              <span
                                onClick={() =>
                                  transactionOverview(item.tx_hash)
                                }
                                className="address"
                              >
                                {item.tx_hash}
                              </span>
                              <CopyAddressClipboard
                                id={index}
                                text={item.tx_hash}
                              />
                            </div>
                          </div>
                        </div>
                        <div className="item">
                          <p>{props.i18n.t('Timestamp')}</p>
                          <div className="values">
                            {moment.utc(item.time * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC
                          </div>
                        </div>
                        <div className="item">
                          <p>{props.i18n.t('Inputs')}</p>
                          <div className="values">
                            <div className="copy-address link-color">
                              <span
                                onClick={() => nodeDetails(item.address)}
                                className="address"
                              >
                                {item.address}
                              </span>
                            </div>
                            <span className="amount">
                              {props.i18n.t('Amount')}:{' '}
                              {getPretty(Amount(Math.abs(item.amount)))} BOA
                            </span>
                            <span className="total-bal mt-2">
                              {getPretty(Amount(Math.abs(item.amount)))} BOA
                            </span>
                          </div>
                        </div>
                        <div className="item">
                          <p>{props.i18n.t('Outputs')}</p>
                          <div className="values">
                            <div className="copy-address link-color">
                              <span
                                onClick={() => nodeDetails(item.peer)}
                                className="address"
                              >
                                {item.peer}
                              </span>
                            </div>
                            <span className="amount">
                              {props.i18n.t('Amount')}:{' '}
                              {getPretty(Amount(Math.abs(item.amount)))} BOA
                            </span>
                            <span className="total-bal mt-2">
                              {getPretty(Amount(Math.abs(item.amount)))} BOA
                            </span>
                          </div>
                        </div>
                        <div className="item">
                          <p>{props.i18n.t('Fee')}</p>
                          <div className="values fees">
                            <span>{getPretty(Amount(item.tx_fee))} BOA</span>
                            <b className="mt-3">
                              {getPretty(item.tx_size)} {props.i18n.t('Bytes')}
                            </b>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}

                  {/* Input/Output Transaction Desktop View  */}
                  {data.map((item: any, index: any) => (
                    <div className="io-desk" key={index}>
                      <div className="io-container">
                        <div className="input">
                          <p>{props.i18n.t('Hash')}</p>
                          <div className="copy-address link-color">
                            <span
                              onClick={() => transactionOverview(item.tx_hash)}
                              className="address"
                            >
                              {item.tx_hash}
                            </span>
                            <CopyAddressClipboard
                              id={item.tx_hash}
                              text={item.tx_hash}
                            />
                          </div>
                          <div className="io-address">
                            <p>{props.i18n.t('Inputs')}</p>
                            <span
                              className="link-color address"
                              onClick={() => nodeDetails(item.address)}
                            >
                              {item.address}
                            </span>
                          </div>
                          <p className="amount">
                            {props.i18n.t('Amount')}:{' '}
                            {getPretty(Amount(Math.abs(item.amount)))} BOA
                          </p>
                          <p className="total-bal mt-2">
                            <span>
                              {props.i18n.t('Total2')}:{' '}
                              {getPretty(Amount(Math.abs(item.amount)))} BOA
                            </span>
                          </p>
                          <p>
                            {props.i18n.t('Type')}: <span>{props.i18n.t(item.tx_type)}</span>
                          </p>
                          <p>
                            {props.i18n.t('Fee')}:{' '}
                            <span>{getPretty(Amount(item.tx_fee))} BOA
                              (
                                {props.i18n.t('ChangeCurrency')}{' '}
                                {getPriceOfFee(item.fee,marketCapStats.price)}
                              )
                            </span>
                          </p>
                          <p>
                            {props.i18n.t('Size')}:{' '}
                            <span>{getPretty(item.tx_size)} {props.i18n.t('Bytes')}</span>
                          </p>
                        </div>
                        <div className="transit">
                          <GoArrowRight />
                        </div>
                        <div className="output">
                          <div className="title-date">
                            <p>{moment.utc(item.time * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC</p>
                            <p/>
                          </div>
                          <p className="v-hide">hide</p>

                          <div className="io-address">
                            <p>{props.i18n.t('Outputs')}</p>
                            <span
                              className="link-color address"
                              onClick={() => nodeDetails(item.peer)}
                            >
                              {item.peer}
                            </span>
                          </div>
                          <p className="amount">
                            {props.i18n.t('Amount')}:{' '}
                            {getPretty(Amount(Math.abs(item.amount)))} BOA
                          </p>
                          <p className="total-bal mt-2">
                            <span>
                              {props.i18n.t('Total2')}:{' '}
                              {getPretty(Amount(Math.abs(item.amount)))} BOA
                            </span>
                          </p>
                        </div>
                      </div>
                    </div>
                  ))}
                </>
              )}
            </div>
            <div className="table-footer">
              <div className="show-list d-lg">
                <p>{props.i18n.t('Show')}</p>
                <div className="select-cont">
                  <select
                    className="form-control"
                    onChange={(e: any) =>
                      numberOfRecordShow(parseInt(e.target.value))
                    }
                  >
                    <option value={20}>20</option>
                    <option value={50}>50</option>
                    <option value={100}>100</option>
                  </select>
                  <ReactSVG src={caretIco} />
                </div>
              </div>
              <div className="pagination_cont">
                <Pagination
                  hideDisabled
                  activePage={currentPage}
                  itemsCountPerPage={showRecord}
                  totalItemsCount={pageCount}
                  pageRangeDisplayed={10}
                  onChange={(e) => handleChangePageNumber('0', e)}
                />
              </div>
              <div className="show-list d-sm">
                <p>{props.i18n.t('Show')}</p>
                <div className="select-cont">
                  <select
                    className="form-control"
                    onChange={(e: any) =>
                      numberOfRecordShow(parseInt(e.target.value))
                    }
                  >
                    <option value={20}>20</option>
                    <option value={50}>50</option>
                    <option value={100}>100</option>
                  </select>
                  <ReactSVG src={caretIco} />
                </div>
              </div>
              <div className="csv-dld">
                <p>{props.i18n.t('Download')}</p>
                <CSVLink data={CSVData} filename={'Transactions List.csv'}>
                  {props.i18n.t('CSV_Export')} <MdFileDownload />
                </CSVLink>
                {/* <Button>
                  {props.i18n.t('CSV_Export')} <MdFileDownload />
                </Button> */}
              </div>
            </div>
          </div>
        </TabPane>
      </TabContent>
    </div>
  )
}

export default withTranslation()(ValidatorTransactionTabs)
