const AnalyticChart: React.FC = (props: any) => {
  // All states for current screen
  const dispatch = useDispatch();
  const searchedData = useSelector((state: any) => state.header.searchedData);
  const [dataState, setDataState] = useState([]);
  const [loading, setLoading] = useState(false);
  const [time, setTime] = useState("D");
  const address = props.match.params['address']
  // var selector = '.timeBtn Button';

  // useEffect(() => {
  //   setLoading(true)
  //   const interval = setInterval(() => {
  //     setDataState((oldDataState: any) => [
  //       ...oldDataState,
  //       {
  //         time: selection[Math.floor(Math.random() * 11)],
  //         price: Math.floor(Math.random() * 50).toString(),
  //         Perc: Math.floor(Math.random() * 30).toString(),
  //       },
  //       setLoading(false)
  //     ]);
  //   }, 2000);

  //   return () => 
  //   setLoading(false);
  //   clearInterval(interval);
  // }, []);

  // get Analytic chart data api
  useEffect(() => {
    setLoading(true)
    request('GET', `${endpoints.holderChart}/` + address + `?filter=${time}&date=${moment(new Date()).utc().unix()}`, {})
      .then((res: any) => {
        const chartData: any = []
        res.data && res.data.forEach((doc: any) =>
          chartData.push([doc.time_stamp * 1000, Amount(doc.balance)]),
        )
        setDataState(chartData)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(true)
      })

  }, [dispatch, time])

  const changeRoute = (route: string) => {
    props.history.push(`${route}/${address}`);
    headerClassUpdater("newclass2", "layout-generic");
  };

  return (
    <div className="page-container">
      <div id="analytic-chart">
        <PageHelmet
          title={`BOSAGORA Explorer - ${props.title}`}
          meta={{ name: "Blocks", content: "Lorem Ipsum" }}
        />
        <Container fluid="xl">
          <Row>
            <Col lg={12} md={12} sm={12}>
              {/* Analytic Chart Top section  */}
              <div className="top-header">
                <div className="left">
                  {/* <div
                    className="back-search"
                    onClick={() => props.history.push(RouterPathEnum.HOLDERS)}
                  >
                    <Link to="#" className="back-link">
                      <AiOutlineArrowLeft />
                      <p>{props.i18n.t("Back")}</p>
                    </Link>
                  </div> */}
                </div>
                <div className="right">
                  <GenericSearchBar
                    history={props.history}
                    searchedDataGet={(searchid: any) =>
                      dispatch(searchDataUpdater(searchid))
                    }
                    searchedData={searchedData}
                  />
                </div>
              </div>
            </Col>
          </Row>
          <Row>
            <Col lg={12} md={12} sm={12}>
              <h2>{props.i18n.t("Analytics_Chart")}</h2>
            </Col>
          </Row>
          <div className="chart-detail">
            <div className="item">
              <p>{props.i18n.t("Address")}:</p>
              <div className="values">
                <div className="copy-address">
                  <span
                    className="link-color address"
                    onClick={() => changeRoute(RouterPathEnum.NODEDETAILS)}
                  >
                    {address}
                  </span>
                  <CopyAddressClipboard id={address} text={address} />
                </div>
              </div>
            </div>
            <div className="timeBtn">
              <button className={time === "D" ? "active" : ""} onClick={() => setTime("D")}>1D</button>
              <button className={time === "5D" ? "active" : ""} onClick={() => setTime("5D")}>5D</button>
              <button className={time === "M" ? "active" : ""} onClick={() => setTime("M")}>1M</button>
              <button className={time === "3M" ? "active" : ""} onClick={() => setTime("3M")}>3M</button>
              <button className={time === "6M" ? "active" : ""} onClick={() => setTime("6M")}>6M</button>
              <button className={time === "Y" ? "active" : ""} onClick={() => setTime("Y")}>1Y</button>
              <button className={time === "3Y" ? "active" : ""} onClick={() => setTime("3Y")}>3Y</button>
              <button className={time === "5Y" ? "active" : ""} onClick={() => setTime("5Y")}>5Y</button>
            </div>
            {/* Analytic Chart section */}
            <div className="analytic-chart">
              {loading ? (
                <div className="chart-loader">
                  <Loader />
                </div>
              ) : (
                dataState.length > 0 ? <LineChart
                  data={[
                    {
                      name: '',
                      data: dataState,
                    },
                  ]}
                /> :
                  <div className="no_data">
                    <h4>{props.i18n.t("NoRecord")}</h4>
                  </div>
              )}
            </div>
          </div>
        </Container>
      </div>
    </div>
  );
};

export default withTranslation()(AnalyticChart);
