import React, { ReactNode, useEffect, useState } from 'react'
import './Header.scss'
import {
  Collapse,
  Navbar,
  NavbarToggler,
  // NavbarBrand,
  Nav,
  NavItem,
  // NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  // NavbarText,
  Container,
} from 'reactstrap'
import { parse as parseQs } from "qs";
import Button from './../Button/Button'
import { AiOutlineGlobal } from 'react-icons/ai'
import { BsFillBrightnessHighFill, BsMoon } from 'react-icons/bs'
import Logo from '../../images/logo.png'
import { Link, NavLink } from 'react-router-dom'
import routes from '../../routes/routes'
import { withTranslation, WithTranslation } from 'react-i18next'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import { Testnet, Coinnet } from "../../config/urlconfigs";
import {networkStatus} from "../../store/header/action";
import {useDispatch} from "react-redux";

export interface HeaderProps extends WithTranslation {
  children?: ReactNode
  onClick?: () => void
  headerClass: string
  headerClassUpdater: Function
  ToggleThem: () => void
  pathname?: any
  theme?: string
}

const Header: React.FC<HeaderProps> = ({
  children,
  onClick,
  headerClass,
  headerClassUpdater,
  i18n,
  ToggleThem,
  theme,
  pathname,
}) => {
  const [isOpen, setIsOpen] = useState(false),
    [hideNav, setHideNav] = useState(false),
    [coinNet, setCoinNet] = useState(true),
    [Language, setLanguage] = useState<any>(i18n.language),
    [activeTab, setActiveTab] = useState(window.location.pathname),
    toggle = () => setIsOpen(!isOpen)

  const dispatch = useDispatch();

  useEffect(() => {
    if (!window.location.pathname.toString().includes(`${RouterPathEnum.BLOCKSDETAILS}/height:`) && !window.location.pathname.toString().includes(`${RouterPathEnum.BLOCKSDETAILS}/hash:`) && !window.location.pathname.toString().includes(RouterPathEnum.TRANSACTIONOVERVIEW) && !window.location.pathname.toString().includes(RouterPathEnum.NODEDETAILS) && !window.location.pathname.toString().includes(RouterPathEnum.ANALYTICCHART) && !window.location.pathname.toString().includes(RouterPathEnum.PROPOSALDETAILS)) {
      let routeExisst = routes.find(route => route['path'] === window.location.pathname);
      if (!routeExisst) {
        pathname.push(RouterPathEnum.PAGENOTFOUND)
      }
    }

    window.addEventListener('resize', resize)
    resize()
    if (window.location.pathname !== '/') {
      headerClassUpdater('newclass2', 'layout-generic')
    }
  })

  useEffect(() => {
    const qs = parseQs(pathname.location.search.substr(1));
    let flag = true;
    if (qs.chain === "testnet") {
      flag = false;
    }
    setCoinNet(flag);
    dispatch(networkStatus(flag));
    setLanguage(localStorage.getItem("i18nextLng"))
  }, [])

  const resize = () => {
    setHideNav(window.innerWidth <= 991)
  }

  useEffect(() => {
    setActiveTab(window.location.pathname)
  }, [window.location.pathname])

  const updateHeaderColor = (route: string) => {
    setActiveTab(route)
    if (hideNav) {
      toggle()
    }
    if (route === '/') {
      headerClassUpdater('newclass', 'layout-home')
      return
    }
    headerClassUpdater('newclass2', 'layout-general')
  }

  const setLanguageLocal = (lang: string) => {
    setLanguage(lang)
    i18n.changeLanguage(lang)
  }

  return <Navbar expand="lg" fixed="top" className={headerClass}>
    <Container fluid="xl">
      <div className="navbar-brand">
        <Link to="/" onClick={() => updateHeaderColor('/')}>
          <img src={Logo} alt="BOASCAN by Bosagora" width="100%" height="100%" />
        </Link>
      </div>
      <NavbarToggler onClick={toggle} />
      <Collapse isOpen={isOpen} navbar>
        <Nav className="me-auto" navbar>
          {routes.map((route, index) => {
            return (
              route.menubar &&
              <NavItem key={index}>
                <NavLink
                  to={route.path}
                  className="nav-link nav-links"
                  activeClassName={activeTab === route.path ? 'nav-lin' : ''}
                  onClick={() => updateHeaderColor(route.path)}
                >
                  {i18n.t(route.name)}
                  <span className="bbr" />
                </NavLink>
              </NavItem>
            )
          })}
          <UncontrolledDropdown className="dropdown-more" nav inNavbar>
            <DropdownToggle nav caret>
              {i18n.t('More')}
            </DropdownToggle>
            <DropdownMenu right>
              <DropdownItem>
                <Link
                  onClick={() => updateHeaderColor('/holders')}
                  to="/holders"
                  className="d-nav-link"
                >
                  {i18n.t('Holders')}
                </Link>
              </DropdownItem>
              <DropdownItem>
                <Link
                  onClick={() => updateHeaderColor('/fee_status')}
                  to="/fee_status"
                  className="d-nav-link"
                >
                  {i18n.t('Fees_Status')}
                </Link>
              </DropdownItem>
              <DropdownItem>
                <Link
                  onClick={() => updateHeaderColor('/terms_service')}
                  to="/terms_service"
                  className="d-nav-link"
                >
                  {i18n.t('Terms_Service')}
                </Link>
              </DropdownItem>
              <DropdownItem>
                <a href="https://rnssolution.github.io/bosagoraMKdocs/" target="_blank" rel="noopener noreferrer" className="d-nav-link">
                  {i18n.t('APIs')}
                </a>
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
          <UncontrolledDropdown className="dropdown-net" nav inNavbar>
            <DropdownToggle nav caret>
              <AiOutlineGlobal className="fa-globe" />
              {coinNet ? <>{i18n.t('CoinNet')}</> : <>{i18n.t('TestNet')}</>}
            </DropdownToggle>
            <DropdownMenu right>
              <DropdownItem className="hide-item">hidden</DropdownItem>
              <DropdownItem>
                <AiOutlineGlobal className="fa-globe" />
                {coinNet ? (
                  <a href={Testnet + "?chain=testnet"} target="_blank" rel="noreferrer noopener">{i18n.t('TestNet')}</a>
                ) : (
                  <a href={Coinnet + "?chain=coinnet"} target="_blank" rel="noreferrer noopener">{i18n.t('CoinNet')}</a>
                )}
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>

          <UncontrolledDropdown className="dropdown-more" nav inNavbar>
            <DropdownToggle nav caret>
              {Language === 'chinese'
                ? '中文'
                : Language === 'korean'
                  ? '한국어'
                  : 'ENG'}
            </DropdownToggle>
            <DropdownMenu right className="lang-dropdown">
              <DropdownItem onClick={() => setLanguageLocal('english')}>
                <Link to="#" className="d-nav-link">
                  English
                </Link>
              </DropdownItem>
              <DropdownItem onClick={() => setLanguageLocal('korean')}>
                <Link to="#" className="d-nav-link">
                  한국어
                </Link>
              </DropdownItem>
              <DropdownItem onClick={() => setLanguageLocal('chinese')}>
                <Link to="#" className="d-nav-link">
                  中文
                </Link>
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
          <Button className="theme-btn">
            {theme === 'dark' ? (
              <BsFillBrightnessHighFill onClick={ToggleThem} />
            ) : (
              <BsMoon onClick={ToggleThem} />
            )}
          </Button>
        </Nav>
      </Collapse>
    </Container>
  </Navbar>
}

export default withTranslation()(Header)
