import './LineChart.scss'
import React from 'react'
import Chart from 'react-apexcharts'
// import ApexCharts from 'apexcharts'
import { ApexOptions } from 'apexcharts'
import { withTranslation, WithTranslation } from 'react-i18next'

export interface LineChartProps extends WithTranslation {
  data: any
  status?: string
}

const LineChartComponent: React.FC<LineChartProps> = ({ data, status, i18n }) => {
  const options: ApexOptions = {
    chart: {
      toolbar: {
        show: false,
      },
      zoom: {
        enabled: false,
      },
      animations: {
        enabled: false,
      },
    },
    markers: {
      colors: ["#014d9a"],
    },
    fill: {
      opacity: 0.9,
      type: 'gradient',
      gradient: {
        // type: 'vertical',
        gradientToColors: ['#1873ce'],
        inverseColors: true,
        opacityFrom: 0.7,
        opacityTo: 0.2,
        stops: [0, 100],
      },
    },
    // legend: { position: 'top', horizontalAlign: 'left' },
    dataLabels: {
      enabled: false,
    },
    stroke: {
      show: true,
      curve: 'smooth',
      colors: ["#004d9a"],
      width: 2,
    },
    xaxis: {
      tooltip: {
        enabled: false
      },
      type: 'datetime',
    },
    yaxis: {
      show: true,
      opposite: true,
      labels: {
        formatter: (val) => {
          if (status === "dashboardChart") {
            return (i18n.language === "korean" ? "₩ " : i18n.language === "chinese" ? "¥ " : "$ ") + val.toFixed(3);
          }
          else if (status === "feeStatusChart") {
            return val.toFixed(7) + i18n.t("BOA_per_a_Byte");
          }
          else {
            return val.toFixed(7) + " BOA";
          }
        }
      }
    },
    tooltip: {
      x: {
        format: 'yyyy-MM-dd HH:mm'
      },
      marker: {
        show: false
      }
    },
  }
  return (
    <Chart options={options} series={data} type="area" width="100%" height="100%" />
  )
}
export default withTranslation()(LineChartComponent);
