import React, { useState, useEffect } from 'react'
import { ReactSVG } from 'react-svg'
import { useDispatch, useSelector } from 'react-redux'
import { GoArrowRight } from 'react-icons/go'
import { MdFileDownload } from 'react-icons/md'
import {useTranslation, withTranslation} from 'react-i18next'
import moment from 'moment'
import caretIco from '../../../images/show-caret.svg'
import endpoints from '../../../config/urlconfigs'
import Loader from 'components/Loader/Loader'
import CopyAddressClipboard from 'components/CopyAddressClipboard/CopyAddressClipboard'
import request from '../../../api/request'
import { CSVLink } from 'react-csv'
import { pageChange } from '../../../store/pagination/thunks'
import { Amount } from 'config/config'
import Pagination from 'react-js-pagination'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import {getPretty, getPriceOfFee} from "../../../utils/CalcUtils";
import {coinMarketCap} from "../../../store/header/thunks";

interface hashHeightData {
  props: any
}
interface MarketCapStats {
  last_updated_at: any
  market_cap: number
  change_24h: number
  price: number
  time: any
  vol_24h: number
}
const TransactionsHashHeight: React.FC<hashHeightData> = (props: any) => {

  // All states for current screen
  const [currentPage, setCurrentPage] = useState(1);
  const [showRecord, setTotalRecords] = useState<any>(20);
  const [transactionsData, setTransactionsData] = useState([]);
  const [pageCount, setPageCount] = useState(0);
  const [CSVData, setCSVData] = useState([]);
  const [loading, setLoading] = useState(true);

  const marketCapStats: MarketCapStats = useSelector(
      (state: any) => state.header.marketCapStats,
  )

  const type = props.props.match.params['hash'].split(':')[0]
  const hash = props.props.match.params['hash'].split(':')[1]

  const dispatch = useDispatch()
  const { t, i18n } = useTranslation();

  useEffect(()=>{
    dispatch(coinMarketCap(i18n.language))
  },[dispatch]);

  useEffect(()=>{
    console.log("");
    dispatch(coinMarketCap(i18n.language))
  },[i18n.language])

  useEffect(() => {
    getEnrolledValidators()
  }, [showRecord, currentPage, hash, type])

  // Get Block Transactions API call
  const getEnrolledValidators = () => {
    request('GET', `${endpoints.blockTransactions}?${type}=${hash}`, {
      pageSize: showRecord,
      page: currentPage,
    })
      .then((res: any) => {
        setTransactionsData(res.data)
        let CSVData: any = []
        setCSVData([])
        for (let i = 0; i < res.data.length; i++) {
          if (window.innerWidth <= 480) {
            CSVData.push({
              Hash: res.data[i].tx_hash,
              Input: res.data[i].sender_address.map((res: any) => {
                return res.address
              }),
              Timestamp: moment.utc(res.data[i].time * 1000).format('YYYY-MM-DD HH:mm:ss') + " UTC",
              Output: res.data[i].receiver.map((res: any) => {
                return res.address
              }),
              OutputAmount: res.data[i].receiver.map((res: any) => {
                return getPretty(Amount(res.amount)) + ' BOA'
              }),
              OutputTotalAmount: getPretty(Amount(res.data[i].amount)) + ' BOA',
              Fee: getPretty(Amount(res.data[i].fee)) + ' BOA',
              Bytes: getPretty(res.data[i].size) + ' Bytes',
            })
          } else {
            CSVData.push({
              Hash: res.data[i].tx_hash,
              Input: res.data[i].sender_address.map((res: any) => {
                return res.address
              }),
              Type: res.data[i].type,
              Fee: getPretty(Amount(res.data[i].fee)) + ' BOA',
              Bytes: getPretty(res.data[i].size) + ' Bytes',
              Timestamp: moment.utc(res.data[i].time * 1000).format('YYYY-MM-DD HH:mm:ss') + " UTC",
              Output: res.data[i].receiver.map((res: any) => {
                return res.address
              }),
              OutputAmount: res.data[i].receiver.map((res: any) => {
                return getPretty(Amount(res.amount)) + ' BOA'
              }),
              OutputTotalAmount: getPretty(Amount(res.data[i].amount)) + ' BOA',
            })
          }
        }
        setCSVData(CSVData)
        setPageCount(res.data[0].full_count)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        return err
      })
  }

  const pagination = useSelector((state: any) => state.pagination)

  useEffect(() => {
    setCurrentPage(pagination.pageNumber)
  }, [pagination.pageNumber])

  const handleChangePageNumber = (e: any, page: number) => {
    dispatch(pageChange(page))
  }

  const numberOfRecordShow = (Record: Number) => {
    setTotalRecords(Record)
    setCurrentPage(1)
  }

  const getOutputTotal = (item: any) => {
    let sum: any = 0
    if (item.receiver) {
      for (let i = 0; i < item.receiver.length; i++) {
        sum = sum + item.receiver[i].amount
      }
      return Amount(sum)
    }
  }

  const getInputTotal = (item: any) => {
    let sum: any = 0
    if (item.sender_address) {
      for (let i = 0; i < item.sender_address.length; i++) {
        sum = sum + item.sender_address[i].amount
      }
      return Amount(sum)
    }
  }

  const transactionOverview = (hash: string) => {
    props.props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${hash}`)
  }

  const nodeDetails = (address: any) => {
    props.props.history.push(`${RouterPathEnum.NODEDETAILS}/${address}`)
  }

  return (
    <>
      <div className="transactionss">
        <div className="header">
          <h2>{t('Transactions')}</h2>
        </div>
        {loading ? (
          <div className="no-data">
            <Loader />
          </div>
        ) : transactionsData.length === 0 ? (
          <div className="no-data">
            <h4>{t('NoRecord')}</h4>
          </div>
        ) : (
          <div className="transactions">
            {/* Block Transaction List Desktop View  */}
            {transactionsData.map((item: any, i: any) => (
              <div className="io-desk" key={i}>
                <div className="io-container">
                  <div className="input">
                    <p>{t('Hash')}</p>
                    <div className="copy-address link-color">
                      <span onClick={() => transactionOverview(item.tx_hash)} className="adrs">{item.tx_hash}</span>
                      <CopyAddressClipboard
                        id={item.tx_hash}
                        text={item.tx_hash}
                      />
                    </div>
                    <div className="io-address">
                      <p>{t('Inputs')}</p>
                      <div className="address_list">
                        {item.sender_address === undefined || item.sender_address === null ||
                          item.sender_address.length === 0
                          ? t('No_Inputs')
                          : item.sender_address.map((info: any, index: any) => (
                            <div key={index}>
                              <span onClick={() => nodeDetails(info.address)} className="link-color address">
                                {info.address}
                              </span>
                              <p className="amount">
                                {t('Amount')}:{' '}
                                {getPretty(Amount(info.amount))} BOA
                              </p>
                            </div>
                          ))}
                      </div>
                    </div>
                    <p className="total-bal mt-2">
                      <span>{t('Total2')}:{' '}{getPretty(getInputTotal(item))} BOA</span>
                    </p>
                    <p className="d-flex">
                      {t('Type')}:&nbsp;<span>{t(item.type)}</span>
                    </p>
                    <p className="d-flex">
                      {t('Fee')}:&nbsp;
                      <span>{getPretty(Amount(item.fee))} BOA (
                        {t('ChangeCurrency')}{' '}
                        {getPriceOfFee(item.fee,marketCapStats.price)})
                      </span>
                    </p>
                    <p className="d-flex">
                      {t('Size')}:&nbsp;
                      <span>{getPretty(item.size)} {t('Bytes')}</span>
                    </p>
                  </div>
                  <div className="transit">
                    <GoArrowRight />
                  </div>
                  <div className="output">
                    <div className="title-date">
                      <p>
                        {moment.utc(item.time * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC
                      </p>
                      <p></p>
                    </div>
                    <p className="v-hide">hide</p>
                    <div className="io-address">
                      <p>{t('Outputs')}</p>
                      <div className="address_list">
                        {item.receiver === undefined ||
                          item.receiver.length === 0
                          ? t('No_Outputs')
                          : item.receiver.map((info: any, index: any) => (
                            <div key={index}>
                              <span onClick={() => nodeDetails(info.address)} className="link-color address">
                                {info.address}
                              </span>
                              <p className="amount">
                                {t('Amount')}:{' '}
                                {getPretty(Amount(info.amount))} BOA
                              </p>
                            </div>
                          ))}
                      </div>
                    </div>
                    <p className="total-bal mt-2">
                      <span>{t('Total2')}:{' '}{getPretty(getOutputTotal(item))} BOA</span>
                    </p>
                  </div>
                </div>
              </div>
            ))}
            {/* Block Transaction List Mobile Responsive View  */}
            {transactionsData.map((item: any, index: any) => (
              <div className="io-res" key={index}>
                <div className="more-detail">
                  <div className="item">
                    <p>{t('Hash')}</p>
                    <div className="values">
                      <div className="copy-address link-color">
                        <span onClick={() => transactionOverview(item.tx_hash)} className="address">{item.tx_hash}</span>
                        <CopyAddressClipboard id={index} text={item.tx_hash} />
                      </div>
                    </div>
                  </div>
                  <div className="item">
                    <p>{t('Inputs')}</p>
                    <div className="values">
                      <div className="address_list">
                        {item.sender_address === undefined || item.sender_address === null ||
                          item.sender_address.length === 0
                          ? t('No_Inputs')
                          : item.sender_address.map((info: any, index: any) => (
                            <div className="brdr" key={index}>
                              <span onClick={() => nodeDetails(info.address)} className="link-color address">
                                {info.address}
                              </span>
                              <div className="amount">
                                <span>{t('Amount')}:</span>
                                <span>
                                  {getPretty(Amount(info.amount))} BOA
                                </span>
                              </div>
                            </div>
                          ))}
                      </div>
                      <span className="total-bal mt-2">
                        {t('Total2')}:{' '}
                        {getPretty(getInputTotal(item))} BOA
                      </span>
                    </div>
                  </div>
                  <div className="item">
                    <p>{t('Timestamp')}</p>
                    <div className="values">
                      {moment.utc(item.time * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC
                    </div>
                  </div>
                  <div className="item">
                    <p>{t('Outputs')}</p>
                    <div className="values">
                      <div className="address_list">
                        {item?.receiver?.map((info: any, index: any) => (
                          <div className="brdr" key={index}>
                            <span onClick={() => nodeDetails(info.address)} className="link-color address">
                              {info.address}
                            </span>
                            <div className="amount">
                              <span>{t('Amount')}:</span>
                              <span>{getPretty(Amount(info.amount))} BOA</span>
                            </div>
                          </div>
                        ))}
                      </div>
                      <span className="total-bal mt-2">
                        {t('Total2')}:{' '}
                        {getPretty(getOutputTotal(item))} BOA
                      </span>
                    </div>
                  </div>
                  <div className="item">
                    <p>{t('Fee')}</p>
                    <div className="values fees">
                      <span>{getPretty(item.fee)}</span>
                      <b className="mt-3">{getPretty(item.size)} {t('Bytes')}</b>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
        {/* Transaction Footer section  */}
        {transactionsData.length === 0 ? (
          ''
        ) : (
          <div className="table-footer">
            <div className="show-list d-lg">
              <p>{t('Show')}</p>
              <div className="select-cont">
                <select
                  className="form-control"
                  onChange={(e) => numberOfRecordShow(parseInt(e.target.value))}
                >
                  <option value={20}>20</option>
                  <option value={50}>50</option>
                  <option value={100}>100</option>
                </select>
                <ReactSVG src={caretIco} />
              </div>
            </div>
            <div className="pagination_cont">
              <Pagination
                hideDisabled
                activePage={currentPage}
                itemsCountPerPage={showRecord}
                totalItemsCount={pageCount}
                pageRangeDisplayed={10}
                onChange={(e) => handleChangePageNumber('0', e)}
              />
            </div>
            <div className="show-list d-sm">
              <p>{t('Show')}</p>
              <select
                className="form-control"
                onChange={(e: any) =>
                  numberOfRecordShow(parseInt(e.target.value))
                }
              >
                <option value={20}>20</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </select>
            </div>
            <div className="csv-dld">
              <p>{t('Download')}</p>
              <CSVLink data={CSVData} filename={'Transactions List.csv'}>
                {t('CSV_Export')} <MdFileDownload />
              </CSVLink>
            </div>
          </div>
        )}
      </div>
    </>
  )
}

export default withTranslation()(TransactionsHashHeight)
