import React, { ReactNode, useState, useEffect } from 'react'
import { InputGroup, Button, Input } from 'reactstrap'
import { AiOutlineSearch } from 'react-icons/ai'
import './SearchBar.scss'
import { withTranslation, WithTranslation } from 'react-i18next'
import endpoints from '../../config/urlconfigs'
import request from '../../api/request'
import * as BoaSdk from "boa-sdk-ts";
import { RouterPathEnum } from 'enums/RouterPathEnum'

var { BOASodium } = require('boa-sodium-ts');

BoaSdk.SodiumHelper.assign(new BOASodium());
export interface SearchBarProps extends WithTranslation {
  children?: ReactNode
  onClick?: () => void
  history?: any
  searchedDataGet: Function
  searchedData: {
    searchType: ''
    available: false
    details: {}
  }
}

const SearchBar: React.FC<SearchBarProps> = ({
  children,
  onClick,
  history,
  i18n,
  searchedDataGet,
  searchedData,
}) => {
  let btn: any

  const [search, setSearch] = useState(''),
    [error, setError] = useState("");

  useEffect(() => {
    BoaSdk.SodiumHelper.init().then(async () => {

    })
  }, [])

  const validatorPublicKey = () => {
    try {
      let respPublickey = new BoaSdk.PublicKey(search)
      return respPublickey.toString();
    }
    catch (err) {
      return false
    }

  }

  // Method to validate hash and decide to redirect to block or transcation
  const validateHash = () => {
    return new Promise((resolve, reject) => {
      request('GET', `${endpoints.searchHash + search}`, {}).then((res: any) => {
        resolve(res.data)
      })
        .catch((err: any) => {
        })
    })
  }


  const showError = (error: string) => {
    setError(error)
    setTimeout(() => {
      setError("")
    }, 5000)
  }

  const searchSubmit = () => {
    if (search) {

      searchedDataGet(search).then((res: any) => {

        let reg = /^[0-9\b]+$/;
        let matchAddress = search.match("boa");
        let matchBlockHash = search.match("0x");
        if (matchAddress) {

          if (validatorPublicKey()) {
            history.push(`${RouterPathEnum.NODEDETAILS}/${search}`)
          }
          else {
            showError("Invalid_Address")
          }

        }
        else if (reg.test(search)) {
          history.push(`${RouterPathEnum.BLOCKSDETAILS}/height:${search}`)
        }
        else if (matchBlockHash) {
          validateHash().then((res: any) => {
            if (res.block || !res) {
              history.push(`${RouterPathEnum.BLOCKSDETAILS}/hash:${search}`)
            }
            else {
              history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${search}`)
            }
          })
            .catch(() => {
              showError('Invalid_Hash')
            })
        }
      })
    }
  }

  const handleKeypress = (e: any) => {
    //it triggers by pressing the enter key
    if (e.charCode === 13) {
      btn.onClick()
    }
  }

  return (
    <div onClick={onClick} id="search-bar">
      <InputGroup>
        <Input
          placeholder={i18n.t(
            'You_may_enter_a_block_height_block_hash_tx_hash_or_address_tx_hash_or_address',
          )}
          onChange={(e) => setSearch(e.target.value)}
          onKeyPress={handleKeypress}
        />
        <Button
          onClick={searchSubmit}
          ref={(node) => (btn = node)}
          type="submit"
        >
          <AiOutlineSearch />
        </Button>
      </InputGroup>
      <div className="errorBox">{error && <p>{i18n.t(error)}</p>}</div>
    </div>
  )
}

export default withTranslation()(SearchBar)
