import Loader from "components/Loader/Loader";
import * as React from "react";
import { Switch } from "react-router-dom";
// import { PageNotFound } from "screens";
// import { FadeIn, ScrollToTopOnMount } from "../components";
//pages
import routes from "./routes";

//Lazy Imports
const PrivateRoute = React.lazy(() => import("./PrivateRoute"));
const PublicRoute = React.lazy(() => import("./PublicRoute"));

const Routes: React.FC = (props: any) => {
  // const [user, setUser] = React.useState<any>(null);
  // const value:any = React.useMemo(() => ({ user, setUser }), [user, setUser]);

  const userRestricted = () => {
    //if user logined, return false
    return false;
  };

  return (
    <Switch>
      <React.Suspense fallback={<div className="page_loader">
        <Loader />
      </div>}>
        {routes.map((route: any, index: number) =>
          route.type === "public" ? (
            <PublicRoute
              restricted={route.restricted ? userRestricted() : false}
              key={route.path}
              {...route}
            />
          ) :
            <PrivateRoute {...route} key={route.path} />
        )}
      </React.Suspense>
    </Switch>
  );
};

export default Routes;
