import React, { useEffect, useState } from 'react'
import { Container, Row, Col, Progress } from 'reactstrap'
import { useSelector, useDispatch } from 'react-redux'
import { PageHelmet, GenericSearchBar, CircularProgress } from 'components'
import { searchDataUpdater } from 'store/header/thunks'
import { withTranslation } from 'react-i18next'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import CopyAddressClipboard from 'components/CopyAddressClipboard/CopyAddressClipboard'
import VotingDetails from './VotingDetails'
import endpoints from '../../../config/urlconfigs'
import request from '../../../api/request'
import { Amount } from '../../../config/config'
import moment from 'moment'

var bigDecimal = require('js-big-decimal')

const getPretty = (val: any) => {
  var value = bigDecimal.getPrettyValue(val)
  return value
}

const ProposalDetails: React.FC = (props: any) => {
  // All states for current screen
  const dispatch = useDispatch()
  const searchedData = useSelector((state: any) => state.header.searchedData)
  const [data, setData] = useState<any>({})
  const proposalID = props.match.params['proposalID']

  useEffect(() => {
    request('GET', `${endpoints.proposalByID}/${proposalID}`, {})
      .then((res: any) => {
        setData(res.data)
      })
      .catch((err: any) => {
        return err
      })
  }, [])

  // Redirect to other screen functions
  const transactionOverview = (hash: string) => {
    props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${hash}`)
  }

  const nodeDetails = (address: string) => {
    props.history.push(`${RouterPathEnum.NODEDETAILS}/${address}`)
  }

  return (
    <div className="page-container">
      <div id="proposal-details">
        <PageHelmet
          title={`BOSAGORA Explorer - ${props.title}`}
          meta={{ name: 'Blocks', content: 'BOSAGORA Explorer - Proposals' }}
        />
        <Container fluid="xl">
          <Row>
            <Col lg={12} md={12} sm={12}>
              {/* Top section  */}
              <div className="top-header">
                <div className="left">
                  {/* <div
                    className="back-search"
                    onClick={() => props.history.push(RouterPathEnum.PROPOSALS)}
                  >
                    <Link to="#" className="back-link">
                      <AiOutlineArrowLeft />
                      <p>{props.i18n.t('Back')}</p>
                    </Link>
                  </div> */}
                </div>
                <div className="right">
                  <GenericSearchBar
                    history={props.history}
                    searchedDataGet={(searchid: any) =>
                      dispatch(searchDataUpdater(searchid))
                    }
                    searchedData={searchedData}
                  />
                </div>
              </div>
            </Col>
          </Row>
          <Row>
            <Col lg={12} md={12} sm={12}>
              <h2>
                {props.i18n.t('Proposal_ID')}. {data.proposal_id}{' '}
                {data.proposal_title}
              </h2>
            </Col>
          </Row>
          {/* Single Proposal summary section */}
          <div className="more-detail">
            {/* <div className="custom-border"></div> */}
            <div className="item">
              <p>{props.i18n.t('Proposal_ID')}</p>
              <div className="values">{data.proposal_id}</div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Proposal_Details')}</p>
              <div className="values">{data.detail}</div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Proposal_TX_Hash')}</p>
              <div className="values">
                {data && data.proposal_tx_hash ?
                  <div className="copy-address">
                    <div
                      onClick={() => transactionOverview(data.proposal_tx_hash)}
                      className="link-color"
                    >
                      {data &&
                        data.proposal_tx_hash &&
                        data.proposal_tx_hash.slice(0, 6) +
                        `…` +
                        data.proposal_tx_hash.slice(-1 * 6)}
                    </div>
                    <CopyAddressClipboard
                      id="proposal_tx_hash"
                      text={data.proposal_tx_hash}
                    />
                  </div>
                  : ''}
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Fee_TX_Hash')}</p>
              <div className="values">
                {data && data.fee_tx_hash ?
                  <div className="copy-address">
                    <div
                      onClick={() => transactionOverview(data.fee_tx_hash)}
                      className="link-color"
                    >
                      {data &&
                        data.fee_tx_hash &&
                        data.fee_tx_hash.slice(0, 6) +
                        `…` +
                        data.fee_tx_hash.slice(-1 * 6)}
                    </div>
                    <CopyAddressClipboard id="fee_tx_hash" text={data.fee_tx_hash} />
                  </div>
                  : ''}
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Proposer')}</p>
              <div className="values">{data.proposer_name}</div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Requested_Amount')}</p>
              <div className="values">
                {data &&
                  data.fund_amount ?
                  getPretty(Amount(data.fund_amount)) : getPretty(data.fund_amount)}{' '}
                BOA
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Proposal_Fee')}</p>
              <div className="values">
                {data &&
                  data.fund_amount ?
                  getPretty(Amount(data.proposal_fee)) : getPretty(data.proposal_fee)}{' '}
                BOA
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Type')}</p>
              <div className="values">{props.i18n.t(data.proposal_type)}</div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Voting_Start')}</p>
              <div className="values">
                {props.i18n.t('Height')}: {getPretty(data.vote_start_height)} / {moment.utc(data.voting_start_date * 1000).format("YYYY-MM-DD HH:mm:ss")} UTC
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Voting_End')}</p>
              <div className="values">
                {props.i18n.t('Height')}: {getPretty(data.vote_end_height)} / {moment.utc(data.voting_end_date * 1000).format("YYYY-MM-DD HH:mm:ss")} UTC
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Proposal_Date')}</p>
              <div className="values">
                {props.i18n.t('Height')}: {getPretty(data.block_height)} / {moment.utc(data.proposal_date * 1000).format("YYYY-MM-DD HH:mm:ss")} UTC
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Proposal_Status')}</p>
              <div className="values">{props.i18n.t(data.proposal_status)}</div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Voting_Progress')}</p>
              <div className="values">
                {props.i18n.t('Total')}: {getPretty(data.total_validators)}{' '}
                {props.i18n.t('Validators')}
              </div>
            </div>
            {data.proposal_status &&
              data.proposal_status !== 'Ongoing' &&
              data.proposal_status !== 'CountingVotes' && (
                <div className="item progress-item border-0">
                  <p className="hide">{props.i18n.t('Voting_Progress')}</p>
                  <div className="values">
                    <div className="progress-cont">
                      <Progress multi>
                        {data.proposal_status === 'Closed' ? (
                          <>
                            <Progress
                              bar
                              color="success"
                              value={
                                parseFloat(data.yes_percentage) &&
                                parseFloat(data.yes_percentage).toFixed(0)
                              }
                            >
                              {parseFloat(data.yes_percentage) &&
                                parseFloat(data.yes_percentage).toFixed(0)}
                              %({data.total_yes_voted})
                            </Progress>
                            <Progress
                              bar
                              color="danger"
                              value={
                                parseFloat(data.no_percentage) &&
                                parseFloat(data.no_percentage).toFixed(0)
                              }
                            >
                              {parseFloat(data.no_percentage) &&
                                parseFloat(data.no_percentage).toFixed(0)}
                              %({data.total_no_voted})
                            </Progress>
                            <Progress
                              bar
                              color="warning"
                              value={
                                parseFloat(data.abstain_percentage) &&
                                parseFloat(data.abstain_percentage).toFixed(0)
                              }
                            >
                              {parseFloat(data.abstain_percentage) &&
                                parseFloat(data.abstain_percentage).toFixed(0)}
                              %({data.total_abstain_voted})
                            </Progress>
                          </>
                        ) : (
                          <Progress
                            bar
                            color="success"
                            value={
                              parseFloat(data.voted_percentage) &&
                              parseFloat(data.voted_percentage).toFixed(0)
                            }
                          >
                            {parseFloat(data.voted_percentage) &&
                              parseFloat(data.voted_percentage).toFixed(0)}
                            %({data.total_voted})
                          </Progress>
                        )}
                        <Progress
                          bar
                          color="info"
                          value={
                            parseFloat(data.not_voted_percentage) &&
                            parseFloat(data.not_voted_percentage).toFixed(0)
                          }
                        >
                          {parseFloat(data.not_voted_percentage) &&
                            parseFloat(data.not_voted_percentage).toFixed(0)}
                          %({data.total_not_voted})
                        </Progress>
                      </Progress>
                    </div>
                    <div className="circular-progress-box">
                      {data.proposal_status === 'Closed' ? (
                        <>
                          <div className="circle-box">
                            <div className="circle-progress-img">
                              <CircularProgress
                                color="circle-progress1"
                                color1="circle-progress1"
                                strokeWidth="5"
                                sqSize="50"
                                percentage={
                                  parseFloat(data.yes_percentage) &&
                                  parseFloat(data.yes_percentage).toFixed(0)
                                }
                              />
                            </div>
                            <div className="circle-progress-cont">
                              <p className="green-text">
                                {props.i18n.t('Yes')}{' '}
                                {parseFloat(data.yes_percentage) &&
                                  parseFloat(data.yes_percentage).toFixed(0)}
                                %
                              </p>
                              <h5>
                                {data.total_yes_voted}{' '}
                                {props.i18n.t('Validators')}
                              </h5>
                            </div>
                          </div>
                          <div className="circle-box">
                            <div className="circle-progress-img">
                              <CircularProgress
                                color="circle-progress3"
                                strokeWidth="5"
                                sqSize="50"
                                percentage={
                                  parseFloat(data.no_percentage) &&
                                  parseFloat(data.no_percentage).toFixed(0)
                                }
                              />
                            </div>
                            <div className="circle-progress-cont">
                              <p className="no-text">
                                {props.i18n.t('No')}{' '}
                                {parseFloat(data.no_percentage) &&
                                  parseFloat(data.no_percentage).toFixed(0)}
                                %
                              </p>
                              <h5>
                                {data.total_no_voted}{' '}
                                {props.i18n.t('Validators')}
                              </h5>
                            </div>
                          </div>
                          <div className="circle-box">
                            <div className="circle-progress-img">
                              <CircularProgress
                                color="circle-progress4"
                                strokeWidth="5"
                                sqSize="50"
                                percentage={
                                  parseFloat(data.abstain_percentage) &&
                                  parseFloat(data.abstain_percentage).toFixed(0)
                                }
                              />
                            </div>
                            <div className="circle-progress-cont">
                              <p className="yellow-text">
                                {props.i18n.t('Abstain')}{' '}
                                {parseFloat(data.abstain_percentage) &&
                                  parseFloat(data.abstain_percentage).toFixed(0)}
                                %
                              </p>
                              <h5>
                                {data.total_abstain_voted}{' '}
                                {props.i18n.t('Validators')}
                              </h5>
                            </div>
                          </div>
                        </>
                      ) : (
                        <div className="circle-box">
                          <div className="circle-progress-img">
                            <CircularProgress
                              color="circle-progress1"
                              color1="circle-progress1"
                              strokeWidth="5"
                              sqSize="50"
                              percentage={
                                parseFloat(data.voted_percentage) &&
                                parseFloat(data.voted_percentage).toFixed(0)
                              }
                            />
                          </div>
                          <div className="circle-progress-cont">
                            <p className="green-text">
                              {props.i18n.t('Voted')}{' '}
                              {parseFloat(data.voted_percentage) &&
                                parseFloat(data.voted_percentage).toFixed(0)}
                              %
                            </p>
                            <h5>
                              {data.total_voted} {props.i18n.t('Validators')}
                            </h5>
                          </div>
                        </div>
                      )}
                      <div className="circle-box">
                        <div className="circle-progress-img">
                          <CircularProgress
                            color="circle-progress5"
                            strokeWidth="5"
                            sqSize="50"
                            percentage={
                              parseFloat(data.not_voted_percentage) &&
                              parseFloat(data.not_voted_percentage).toFixed(0)
                            }
                          />
                        </div>
                        <div className="circle-progress-cont">
                          <p className="mehroon-text">
                            {props.i18n.t('Not_Voted')}{' '}
                            {parseFloat(data.not_voted_percentage) &&
                              parseFloat(data.not_voted_percentage).toFixed(0)}
                            %
                          </p>
                          <h5>
                            {data.total_not_voted} {props.i18n.t('Validators')}
                          </h5>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              )}
          </div>
          <div className="header">
            <h2 className="mb-32">{props.i18n.t('Pre_Evaluation')}</h2>
          </div>
          <div className="more-detail">
            {/* <div className="custom-border"></div> */}
            <div className="item">
              <p>{props.i18n.t('Pre_Evaluation_Starting_Time')}</p>
              <div className="values">
                {moment.utc(data.pre_evaluation_start_time * 1000).format(
                  'YYYY-MM-DD HH:mm:ss',
                )} UTC
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Pre_Evaluation_Ending_Time')}</p>
              <div className="values">
                {moment.utc(data.pre_evaluation_end_time * 1000).format(
                  'YYYY-MM-DD HH:mm:ss',
                )} UTC
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Average_Pre_evaluation_Score')}</p>
              <div className="values">
                {getPretty(data.ave_pre_evaluation_score)}
              </div>
            </div>
            <div className="item">
              <p className="link-url">{props.i18n.t('Attachment_URL')}</p>
              <div className="values">
                {data &&
                  data.urls &&
                  data.urls.map((res: any, index: any) => (
                    <a
                      key={index}
                      href={res.url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      {res.url}
                    </a>
                  ))}
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Proposer_Wallet_Address')}</p>
              <div className="values">
                {data && data.proposer_address ?
                  <div className="copy-address max_address">
                    <div
                      onClick={() => nodeDetails(data.proposer_address)}
                      className="link-color"
                    >
                      {data.proposer_address &&
                        data.proposer_address.slice(0, 6) +
                        `…` +
                        data.proposer_address.slice(-1 * 6)}
                    </div>
                    <CopyAddressClipboard id={data.proposer_address} text={data.proposer_address} />
                  </div>
                  : ''}
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Wallet_Address_to_Deposit')}</p>
              <div className="values">
                {data && data.proposal_fee_address ?
                  <div className="copy-address max_address">
                    <div
                      onClick={() => nodeDetails(data.proposal_fee_address)}
                      className="link-color"
                    >
                      {data.proposal_fee_address &&
                        data.proposal_fee_address.slice(0, 6) +
                        `…` +
                        data.proposal_fee_address.slice(-1 * 6)}
                    </div>
                    <CopyAddressClipboard id={data.proposal_fee_address} text={data.proposal_fee_address} />
                  </div>
                  : ''}
              </div>
            </div>
            {/* <div className="item">
              <p>{props.i18n.t('Voting_Fee_Hash')}</p>
              <div className="values">
                <div className="copy-address">
                  <div
                    onClick={() =>
                      transactionOverview(PreEvaluationSTats.Voting_Fee_Hash)
                    }
                    className="link-color"
                  >
                    {PreEvaluationSTats.Voting_Fee_Hash.slice(0, 6) +
                      `…` +
                      PreEvaluationSTats.Voting_Fee_Hash.slice(-1 * 6)}
                  </div>
                  <CopyAddressClipboard
                    text={PreEvaluationSTats.Voting_Fee_Hash}
                  />
                </div>
              </div>
            </div>
            <div className="item">
              <p>{props.i18n.t('Proposing_Fee_Hash')}</p>
              <div className="values">
                <div className="copy-address">
                  <div
                    onClick={() =>
                      transactionOverview(PreEvaluationSTats.Voting_Fee_Hash)
                    }
                    className="link-color"
                  >
                    {PreEvaluationSTats.Voting_Fee_Hash.slice(0, 6) +
                      `…` +
                      PreEvaluationSTats.Voting_Fee_Hash.slice(-1 * 6)}
                  </div>
                  <CopyAddressClipboard
                    text={PreEvaluationSTats.Voting_Fee_Hash}
                  />
                </div>
              </div>
            </div> */}
          </div>
          {/* Voting Details component */}
          <VotingDetails props={props} proposalID={proposalID} />
        </Container>
      </div>
    </div>
  )
}

export default withTranslation()(ProposalDetails)
