import React, { useEffect, useState } from "react";
import { MobileHeader, Header, Footer } from "./components";
import Routes from "./routes";
import { withTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { headerClassUpdater } from "./store/header/thunks";
import { withRouter } from "react-router-dom";
// Theme for dark mode...
import { ThemeProvider } from "styled-components";
import { lightTheme, darkTheme } from "darkMode/theme";
import { GlobalStyles } from "darkMode/global";

const App: React.FC<any> = ({ i18n, history }) => {
  const dispatch = useDispatch();
  const [headerLanding, setHeaderLanding] = useState("newclass");

  const headerClass = useSelector((state: any) => state.header.headerClass);
  useEffect(() => {
    const listenScrollEvent = () => {
      if (window.scrollY < 30) {
        setHeaderLanding("newclass");
      } else {
        setHeaderLanding("newclass2");
      }
    };
    window.addEventListener("scroll", listenScrollEvent);
  }, []);

  const [theme, setTheme] = useState("light");
  useEffect(() => {
    i18n.changeLanguage(localStorage.getItem("i18nextLng"));
    const localTheme = window.localStorage.getItem("theme");
    localTheme && setTheme(localTheme);
  }, []);

  const themeMode = theme === "light" ? lightTheme : darkTheme;

  const ToggleThem = () => {
    if (theme === "light") {
      window.localStorage.setItem("theme", "dark");
      setTheme("dark");
    } else {
      window.localStorage.setItem("theme", "light");
      setTheme("light");
    }
  };

  return (
    // <div className={layoutClass}>
    <div className="main">
      <ThemeProvider theme={themeMode}>
        <GlobalStyles />
        {/* {window.innerWidth <= 991 ? ( */}
        <div className="Mobile-Header">
          <MobileHeader
            headerClass={
              window.location.pathname === "/" ? headerLanding : headerClass
            }
            pathname={history}
            headerClassUpdater={(
              changeHeaderClassName: any,
              changeLayoutClassName: any
            ) =>
              dispatch(
                headerClassUpdater(
                  changeHeaderClassName,
                  changeLayoutClassName
                )
              )
            }
            ToggleThem={ToggleThem}
            theme={theme}
          />
        </div>
        {/* ) : ( */}
        <div className="Desktop-Header">
          <Header
            headerClass={
              window.location.pathname === "/" ? headerLanding : headerClass
            }
            pathname={history}
            headerClassUpdater={(
              changeHeaderClassName: any,
              changeLayoutClassName: any
            ) =>
              dispatch(
                headerClassUpdater(changeHeaderClassName, changeLayoutClassName)
              )
            }
            ToggleThem={ToggleThem}
            theme={theme}
          />
        </div>
        {/* )} */}
        <Routes />
        <Footer />
      </ThemeProvider>
    </div>
  );
};

export default withTranslation()(withRouter(App))
