import React, { useState, useEffect } from 'react'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import { useDispatch, useSelector } from 'react-redux'
import { Table } from '../../../components'
import { pageChange } from '../../../store/pagination/thunks'
import { withTranslation } from 'react-i18next'
import request from '../../../api/request'
import endpoints from '../../../config/urlconfigs'
import { Amount } from 'config/config'
var bigDecimal = require('js-big-decimal')

const getPretty = (val: any) => {
  var value = bigDecimal.getPrettyValue(val)
  return value
}
const VotingRewardTabs: React.FC<any> = (props: any) => {
  const dispatch = useDispatch()
  const pagination = useSelector((state: any) => state.pagination)
  const [showRecord, setTotalRecords] = useState<any>(20)
  const [currentPage, setCurrentPage] = useState(1)
  const [pageCount, setPageCount] = useState(0)
  const [loading, setLoading] = useState(false);
  const [rewardList, setRewardList] = useState([]);
  const [CSVData, setCSVData] = useState([]);

  const numberOfRecordShow = (Record: Number) => {
    setTotalRecords(Record)
    setCurrentPage(1)
  }

  useEffect(() => {
    setCurrentPage(pagination.pageNumber)
  }, [pagination.pageNumber])


  const blockDetails = (height: string) => {
    props.props.props.history.push(`${RouterPathEnum.BLOCKSDETAILS}/height:${height}`)
  }

  useEffect(() => {
    getRewardList()
  }, [showRecord, currentPage, props.address])


  const getRewardList = (() => {
    setLoading(true)
    request('GET', `${endpoints.validatorReward}/${props.address}`, { pageSize: showRecord, page: currentPage })
      .then((res: any) => {
        setRewardList(res.data.length === 0 ? [] : res.data)
        let CSVData: any = []
        setCSVData([])
        if (res.data) {
          for (let i = 0; i < res.data.length; i++) {
            CSVData.push({
              Height: getPretty(res.data[i].block_height),
              FreezingForValidatorAmount: getPretty(Amount(res.data[i].steaking_amount)) + " BOA",
              BlockTotalReward: getPretty(Amount(res.data[i].block_reward)) + " BOA",
              BlockTotalFees: getPretty(Amount(res.data[i].block_fee)) + " BOA",
              NodeReceiveReward: getPretty(Amount(res.data[i].validator_reward)) + " BOA",
            })
          }
        }
        setCSVData(CSVData)
        setPageCount(res.data && res.data[0].full_count)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        return err
      })
  })

  return (
    <>
      <div className="reward-tab">
        {/* <h2>{props.i18n.t('Reward_List')}</h2> */}
        {/* Rewards list */}
        <Table
          headerData={[
            {
              Height: '',
              Freezing_for_validator_Amount: '',
              Block_Total_Reward: '',
              Block_Total_Fees: '',
              Node_Receive_Reward: '',
            },
          ]}
          headData={
            rewardList.map((reward: any, index: number) => {
              return {
                Height: (
                  <div
                    onClick={() => blockDetails(reward.block_height)}
                    className="link-color"
                  >
                    {getPretty(reward.block_height)}
                  </div>
                ),
                Freezing_for_validator_Amount: (
                  <>{getPretty(Amount(reward.steaking_amount))} BOA</>
                ),
                Block_Total_Reward: (
                  <>{getPretty(Amount(reward.block_reward))} BOA</>
                ),
                Block_Total_Fees: <>{getPretty(Amount(reward.block_fee))} BOA </>,
                Node_Receive_Reward: (
                  <>{getPretty(Amount(reward.validator_reward))} BOA </>
                ),
              }
            })
          }
          loading={loading}
          currentPage={currentPage}
          pageChange={(pageNumber: number) =>
            dispatch(pageChange(pageNumber))
          }
          showRecord={showRecord}
          pageCount={pageCount}
          props={props}
          data={rewardList}
          CSVData={CSVData}
          fileName={'Reward Data.csv'}
          numberOfRecordShow={(Record: number) => numberOfRecordShow(Record)}
        />
      </div>
    </>

  )
}

export default withTranslation()(VotingRewardTabs)
