import React, { useEffect, useState } from 'react'
import { Container, Row, Col, Card } from 'reactstrap'
import { PageHelmet } from 'components'
import english from '../../images/english.png'
import korean from '../../images/korean.png'
import chinese from '../../images/chinese.png'
import { withTranslation } from 'react-i18next'

const Languages: React.FC = (props: any) => {
  const [Language, setLanguage] = useState(props.i18n.language)

  useEffect(() => {
    setLanguage(localStorage.getItem("i18nextLng"))
  }, [])

  const setLanguageLocal = (lang: string) => {
    setLanguage(lang)
    props.i18n.changeLanguage(lang)
    props.history.push("/")
  }

  return (
    <div className="page-container">
      <div id="languages">
        <PageHelmet
          title={props.i18n.t(props.title)}
          meta={{ name: 'Languages', content: 'Lorem Ipsum' }}
        />
        <Container fluid="xl">
          <Row>
            <Col lg={12} md={12} sm={12}>
              <h2>{props.i18n.t(props.title)}</h2>
            </Col>
          </Row>
          <div className="lang-list">
            <ul>
              <li className={(Language !== 'korean' && Language !== 'chinese') ? "active" : ""}>
                <Card onClick={() => setLanguageLocal('english')}>
                  <div className="icon">
                    <img src={english} alt="English" width="100%" height="100%" />
                  </div>
                  <p>English</p>
                </Card>
              </li>
              <li className={Language === "korean" ? "active" : ""}>
                <Card onClick={() => setLanguageLocal('korean')}>
                  <div className="icon">
                    <img src={korean} alt="Korean" width="100%" height="100%" />
                  </div>
                  <p>한국어</p>
                </Card>
              </li>
              <li className={Language === "chinese" ? "active" : ""}>
                <Card onClick={() => setLanguageLocal('chinese')}>
                  <div className="icon">
                    <img src={chinese} alt="Chinese" width="100%" height="100%" />
                  </div>
                  <p>中文</p>
                </Card>
              </li>
            </ul>
          </div>
        </Container>
      </div>
    </div>
  )
}

export default withTranslation()(Languages);
