import React, { useState, useEffect } from 'react'
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
} from 'reactstrap'
import classnames from 'classnames'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import { useDispatch, useSelector } from 'react-redux'
import { Table } from '../../../components'
import { pageChange } from '../../../store/pagination/thunks'
import { withTranslation } from 'react-i18next'
import CopyAddressClipboard from 'components/CopyAddressClipboard/CopyAddressClipboard'
import request from '../../../api/request'
import endpoints from '../../../config/urlconfigs'
import RewardList from './rewardList'
var bigDecimal = require('js-big-decimal')

const getPretty = (val: any) => {
  var value = bigDecimal.getPrettyValue(val)
  return value
}
const VotingRewardTabs: React.FC<any> = (props: any) => {
  const dispatch = useDispatch()
  const pagination = useSelector((state: any) => state.pagination)
  const [showRecord, setTotalRecords] = useState<any>(20)
  const [currentPage, setCurrentPage] = useState(1)
  const [pageCount, setPageCount] = useState(0)
  const [loading, setLoading] = useState(false);
  const [votingList, setVotingList] = useState([]);
  const [CSVData, setCSVData] = useState([]);

  const numberOfRecordShow = (Record: Number) => {
    setTotalRecords(Record)
    setCurrentPage(1)
  }

  useEffect(() => {
    setCurrentPage(pagination.pageNumber)
  }, [pagination.pageNumber])


  const [activeTab1, setActiveTab1] = useState('1')

  const toggle1 = (tab: any) => {
    setActiveTab1(tab)
  }

  const transactionOverview = (hash: string) => {
    props.props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${hash}`)
  }

  const proposalOverview = (proposalID: string) => {
    props.props.history.push(`${RouterPathEnum.PROPOSALDETAILS}/${proposalID}`)
  }

  let numberIndex = 0
  let numberIndex2 = 0
  numberIndex = showRecord * (currentPage === 0 ? 0 : (currentPage) - 1)
  numberIndex2 = showRecord * (currentPage === 0 ? 0 : (currentPage) - 1)

  useEffect(() => {
    getVotingList()
  }, [showRecord, currentPage, props.address])

  const getVotingList = (() => {
    setLoading(true)
    request('GET', `${endpoints.validatorBallot}/${props.address}`, { pageSize: showRecord, page: currentPage })
      .then((res: any) => {
        setVotingList(res.data.length === 0 ? [] : res.data)
        let CSVData: any = []
        setCSVData([])
        for (let i = 0; i < res.data.length; i++) {
          numberIndex2 = numberIndex2 + 1
          CSVData.push({
            No: numberIndex2,
            ProposalID: res.data[i].proposal_id,
            Hash: res.data[i].tx_hash,
            Sequence: res.data[i].sequence,
            ProposalType: res.data[i].proposal_type,
            ProposalTitle: res.data[i].proposal_title,
            BallotChoice: res.data[i].ballot_answer,
          })
        }
        setCSVData(CSVData)
        setPageCount(res.data && res.data[0].full_count)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        return err
      })
  })
  return (
    <div className="voting-reward-tabs">
      {/* Tabs Header part */}
      <Nav tabs className="p-0">
        <NavItem className="ml-0">
          <NavLink
            className={classnames({ active: activeTab1 === '1' })}
            onClick={() => {
              toggle1('1')
            }}
          >
            {props.i18n.t('Reward_List')}
          </NavLink>
        </NavItem>
        <NavItem className="ml-0">
          <NavLink
            className={classnames({ active: activeTab1 === '2' })}
            onClick={() => {
              toggle1('2')
            }}
          >
            {props.i18n.t('Voting_History')}
          </NavLink>
        </NavItem>
      </Nav>
      {/* Tabs Content part */}
      <TabContent activeTab={activeTab1}>
        <TabPane tabId="1">
          <RewardList address={props.address} props={props} />
        </TabPane>
        <TabPane tabId="2">
          <div className="voting-tab">
            {/* Voting History list */}
            <Table
                headerData={[
                  {
                    No: '',
                    Proposal_ID: '',
                    Hash: '',
                    Sequence: '',
                    Proposal_Type: '',
                    Proposal_Title: '',
                    Ballot_Choice: '',
                  },
                ]}
                headData={votingList.map((votingData: any, index: number) => {
                  numberIndex = numberIndex + 1
                  return {
                    No: numberIndex,
                    Proposal_ID: (
                        <div onClick={() => proposalOverview(votingData.proposal_id)} className="link-color">
                          <span className="address">{votingData.proposal_id}</span>
                        </div>
                    ),
                    Hash: (
                        <div className="copy-address">
                          <div
                              onClick={() => transactionOverview(votingData.tx_hash)}
                              className="link-color"
                          >
                            {votingData.tx_hash.slice(0, 6) +
                                `…` +
                                votingData.tx_hash.slice(-1 * 6)}
                          </div>
                          <CopyAddressClipboard id={votingData.tx_hash} text={votingData.tx_hash} />
                        </div>
                    ),
                    Sequence: <>{getPretty(votingData.sequence)}</>,
                    Proposal_Type: props.i18n.t(votingData.proposal_type),
                    Proposal_Title: votingData.proposal_title,
                    // votingEndHeight: votingData.votingEndHeight,
                    Ballot_Choice: props.i18n.t(votingData.ballot_answer),
                  }
                })}
                loading={loading}
                currentPage={currentPage}
                pageCount={pageCount}
                pageChange={(pageNumber: number) =>
                    dispatch(pageChange(pageNumber))
                }
                showRecord={showRecord}
                CSVData={CSVData}
                props={props}
                data={votingList ? votingList : []}
                fileName={'Voting History.csv'}
                numberOfRecordShow={(Record: number) => numberOfRecordShow(Record)}
            />
          </div>
        </TabPane>
      </TabContent>
    </div>
  )
}

export default withTranslation()(VotingRewardTabs)
