import React, { useEffect } from "react";
import { Container, Row, Col } from "reactstrap";

// import { RouterPathEnum } from 'enums/RouterPathEnum'
import "./API.scss";
import { GenericSearchBar, PageHelmet } from "components";
import { searchDataUpdater } from "store/header/thunks";
import { useDispatch, useSelector } from "react-redux";

const API: React.FC = (props: any) => {
  const dispatch = useDispatch();
  const searchedData = useSelector((state: any) => state.header.searchedData);

  useEffect(
    () => {
      let isDown = false;
      // let startX: any;
      // let scrollLeft: any;

      window.addEventListener("mousedown", (e: any) => {
        isDown = true;
        // ref && ref.current.classList.add('active');
        // startX = e.pageX - ref.current.offsetLeft;
        // scrollLeft = ref.current.scrollLeft;
      });
      window.addEventListener("mouseleave", () => {
        isDown = false;
        // ref.current.classList.remove('active');
      });
      window.addEventListener("mouseup", () => {
        isDown = false;
        // ref.current.classList.remove('active');
      });
      window.addEventListener("mousemove", (e) => {
        if (!isDown) {
          return;
        }
        e.preventDefault();
        // const x = e.pageX - ref.current.offsetLeft;
        // const walk = (x - startX) * 3;
        // ref.current.scrollLeft = scrollLeft - walk;
      });
    }
    // [ref]
  );

  return (
    <div className="page-container">
      <div id="api">
        <PageHelmet
          title={props.title}
          meta={{ name: "BOA API", content: "Lorem Ipsum" }}
        />
        <Container fluid="xl">
          <Row>
            <Col xl={6} lg={6} md={12} sm={12}></Col>
            <Col xl={6} lg={6} md={12} sm={12}>
              <GenericSearchBar
                history={props.history}
                searchedDataGet={(searchid: string) =>
                  dispatch(searchDataUpdater(searchid))
                }
                searchedData={searchedData}
              />
            </Col>
          </Row>
          <Row>
            <Col lg={12} md={12} sm={12}>
              <h2>{props.title}</h2>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
};

export default API;
