import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Table } from 'components'
import { pageChange } from 'store/pagination/thunks'
import { withTranslation } from 'react-i18next'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import CopyAddressClipboard from 'components/CopyAddressClipboard/CopyAddressClipboard'
import request from '../../../api/request'
import endpoints from '../../../config/urlconfigs'
import moment from 'moment'
var bigDecimal = require('js-big-decimal')

const VotingDetails: React.FC<any> = (props: any) => {
  const dispatch = useDispatch()
  const pagination = useSelector((state: any) => state.pagination)
  const [data, setData] = useState<any>([])
  const [CSVData, setCSVData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [pageCount, setPageCount] = useState(0)
  const [loading, setLoading] = useState(false)
  const [showRecord, setTotalRecords] = useState<any>(20)

  useEffect(() => {
    getVotingDetails()
  }, [showRecord, currentPage])

  const getPretty = (val: any) => {
    var value = bigDecimal.getPrettyValue(val)
    return value
  }

  const getVotingDetails = () => {
    setLoading(true)
    request('GET', `${endpoints.proposalVotingDetails}/${props.proposalID}`, { pageSize: showRecord, page: currentPage })
      .then((res: any) => {
        setData(res.data)
        let CSVData: any = []
        setCSVData([])
        for (let i = 0; i < res.data.length; i++) {
          CSVData.push({
            VoterUTXOKey: res.data[i].voter_utxo_key,
            Address: res.data[i].address,
            Sequence: res.data[i].sequence,
            Hash: res.data[i].hash,
            BallotAnswer: res.data[i].ballot_answer,
            VotingTime: moment.utc(res.data[i].voting_time * 1000).format("YYYY-MM-DD HH:mm:ss") + " UTC",
          })
        }
        setCSVData(CSVData)
        setPageCount(res.data[0].full_count)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        return err
      })
  }

  const numberOfRecordShow = (Record: Number) => {
    setTotalRecords(Record)
    setCurrentPage(1)
  }

  useEffect(() => {
    setCurrentPage(pagination.pageNumber)
  }, [pagination.pageNumber])

  // Redirect to other screen functions
  const transactionOverview = (hash: string) => {
    props.props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${hash}`)
  }

  const transactionOverviewUTXOKey = (utxoKey: string) => {
    props.props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${utxoKey}`, { utxoKey: true })
  }

  const nodeDetails = (address: string) => {
    props.props.history.push(`${RouterPathEnum.NODEDETAILS}/${address}`)
  }

  return (
    <div className="voting-detail">
      <div className="header">
        <h2>{props.i18n.t('Voting_Details')}</h2>
      </div>
      {/* Voting Details list */}
      <Table
        headerData={[
          {
            Voter_UTXO_Key: '',
            Address: '',
            Sequence: '',
            Hash: '',
            Ballot_Answer: '',
            Voting_Time: '',
          },
        ]}
        headData={data.map((data: any, index: number) => {
          return {
            Voter_UTXO_Key: (
              <div className="copy-address max_address">
                <div onClick={() => transactionOverviewUTXOKey(data.voter_utxo_key)} className="link-color">
                  {data.voter_utxo_key.slice(0, 6) + `…` + data.voter_utxo_key.slice(-1 * 6)}
                </div>
                <CopyAddressClipboard id={data.voter_utxo_key} text={data.voter_utxo_key} />
              </div>
            ),
            Address: (
              <div className="copy-address max_address">
                <div onClick={() => nodeDetails(data.address)} className="link-color">
                  {data.address.slice(0, 6) + `…` + data.address.slice(-1 * 6)}
                </div>
                <CopyAddressClipboard id={data.address} text={data.address} />
              </div>
            ),
            Sequence: getPretty(data.sequence),
            Hash: (
              <div className="copy-address max_address">
                <div
                  onClick={() => transactionOverview(data.hash)}
                  className="link-color"
                >
                  {data.hash.slice(0, 6) + `…` + data.hash.slice(-1 * 6)}
                </div>
                <CopyAddressClipboard id={data.hash} text={data.hash} />
              </div>
            ),
            Ballot_Answer: props.i18n.t(data.ballot_answer),
            Voting_Time: moment.utc(data.voting_time * 1000).format("YYYY-MM-DD HH:mm:ss") + " UTC",
          }
        })}
        loading={loading}
        currentPage={currentPage}
        pageCount={pageCount}
        pageChange={(pageNumber: number) => dispatch(pageChange(pageNumber))}
        props={props}
        showRecord={showRecord}
        CSVData={CSVData}
        data={data}
        fileName={'Voting List.csv'}
        numberOfRecordShow={(Record: number) => numberOfRecordShow(Record)}
      />
    </div>
  )
}

export default withTranslation()(VotingDetails)
