import React, { useState, useEffect } from 'react'
import { Container, Row, Col } from 'reactstrap'
import { useDispatch, useSelector } from 'react-redux'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import { GenericSearchBar, PageHelmet, Table } from 'components'
import { searchDataUpdater } from 'store/header/thunks'
import { pageSizeChange } from 'store/paginationSize/thunks'
import upchart from 'images/upchart.svg'
import { withTranslation } from 'react-i18next'
import HoldersStatus from "./HolderStatus"
import endpoints from '../../config/urlconfigs'
import request from '../../api/request'
import { pageChange } from 'store/pagination/thunks'
import { Amount } from 'config/config'

var bigDecimal = require('js-big-decimal')

const Holders: React.FC = (props: any) => {
  // All states for current screen
  const dispatch = useDispatch()
  const searchedData = useSelector((state: any) => state.header.searchedData)
  const pagination = useSelector((state: any) => state.pagination)

  const [sortingStatus, setSortingStatus] = useState({
    Balance: true,
    Freezing: true,
    RecivedReward: true,
  }),
    [holdersList, setHolderList] = useState([]),
    [showRecord, setTotalRecords] = useState<any>(20),
    [pageCount, setPageCount] = useState(0),
    [loading, setLoading] = useState(false),
    [load, setLoad] = useState(false),
    [CSVData, setCSVData] = useState([]),
    [currentPage, setCurrentPage] = useState(1);

  // useEffect(() => {
  //   getHolders()
  // }, [])

  let numberIndex = 0
  numberIndex = showRecord * (currentPage === 0 ? 0 : (currentPage) - 1)

  useEffect(() => {
    setCurrentPage(pagination.pageNumber)
  }, [pagination])

  useEffect(() => {
    getHolders()
  }, [showRecord, currentPage, props.i18n.language])

  const getPretty = (val: any) => {
    var value = bigDecimal.getPrettyValue(val)
    return value
  }

  // get all holder list api
  const getHolders = () => {
    setLoading(true)
    request("GET", `${endpoints.holders}?currency=${props.i18n.language === "korean" ? "krw" : props.i18n.language === "chinese" ? "cny" : "usd"}`, { pageSize: showRecord, page: currentPage })
      .then((res: any) => {
        setLoading(false)
        let CSVData: any = []
        let data: any = []
        setCSVData([])
        for (let i = 0; i < res.data.length; i++) {
          numberIndex = numberIndex + 1
          data.push({
            index: numberIndex,
            address: res.data[i].address,
            total_balance: res.data[i].total_balance,
            total_frozen: res.data[i].total_frozen,
            total_reward: res.data[i].total_reward,
            percentage: res.data[i].percentage,
            value: res.data[i].value,
          })
          CSVData.push({
            No: numberIndex,
            Address: res.data[i].address,
            Balance: parseFloat(Amount(res.data[i].total_balance)).toLocaleString('en-US', { maximumFractionDigits: 4, minimumFractionDigits: 4 }) + " BOA",
            Freezing: getPretty(Amount(res.data[i].total_frozen)) + " BOA",
            ReceivedRewards: parseFloat(Amount(res.data[i].total_reward)).toLocaleString('en-US', { maximumFractionDigits: 4, minimumFractionDigits: 4 }) + " BOA",
            Percentage: parseFloat(res.data[i].percentage).toFixed(2) + "%",
            Value: props.i18n.t("ChangeCurrency") + getPretty(res.data[i].value),
          })
        }
        setCSVData(CSVData)
        setHolderList(data)
        setPageCount(res.data[0].full_count)
      })
      .catch((err: any) => {
        return err;
      });
  }

  useEffect(
    () => {
      let isDown = false
      // let startX: any;
      // let scrollLeft: any;
      window.addEventListener('mousedown', (e: any) => {
        isDown = true
        // ref && ref.current.classList.add('active');
        // startX = e.pageX - ref.current.offsetLeft;
        // scrollLeft = ref.current.scrollLeft;
      })
      window.addEventListener('mouseleave', () => {
        isDown = false
        // ref.current.classList.remove('active');
      })
      window.addEventListener('mouseup', () => {
        isDown = false
        // ref.current.classList.remove('active');
      })
      window.addEventListener('mousemove', (e) => {
        if (!isDown) {
          return
        }
        e.preventDefault()
        // const x = e.pageX - ref.current.offsetLeft;
        // const walk = (x - startX) * 3;
        // ref.current.scrollLeft = scrollLeft - walk;
      })
    },
    // [ref]
  )

  // Method to sort holder list base on balance,freezing and received balance
  const sortingData = (typeSort: string) => {
    if (typeSort === 'Balance') {
      const old = sortingStatus
      old.Balance = !old.Balance
      setSortingStatus(old)
      let sortByBalance = holdersList.sort((a: any, b: any) => {
        return sortingStatus.Balance
          ? parseInt(b.total_balance) - parseInt(a.total_balance)
          : parseInt(a.total_balance) - parseInt(b.total_balance)
      })
      setLoad(!load)
      setHolderList(sortByBalance)
    } else if (typeSort === 'Freezing') {
      const old = sortingStatus
      old.Freezing = !old.Freezing
      setSortingStatus(old)
      let sortByFreezing = holdersList.sort((a: any, b: any) => {
        return sortingStatus.Freezing
          ? parseInt(b.total_frozen) - parseInt(a.total_frozen)
          : parseInt(a.total_frozen) - parseInt(b.total_frozen)
      })
      setLoad(!load)
      setHolderList(sortByFreezing)
    } else {
      const old = sortingStatus
      old.RecivedReward = !old.RecivedReward
      setSortingStatus(old)
      let sortByReward = holdersList.sort((a: any, b: any) => {
        return sortingStatus.RecivedReward
          ? parseInt(b.total_reward) - parseInt(a.total_reward)
          : parseInt(a.total_reward) - parseInt(b.total_reward)
      })
      setLoad(!load)
      setHolderList(sortByReward)
    }
  }

  // useEffect(() => {
  //   sortingData("Balance");
  // }, [sortingData]);

  const analyticChart = (address: any) => {
    props.history.push(`${RouterPathEnum.ANALYTICCHART}/${address}`)
  }

  const nodeDetails = (address: string) => {
    props.history.push(`${RouterPathEnum.NODEDETAILS}/${address}`)
  }

  const numberOfRecordShow = (Record: Number) => {
    setTotalRecords(Record)
    setCurrentPage(1)
  }

  return (
    <div className="page-container">
      <div id="holders">
        <PageHelmet
          title={`BOSAGORA Explorer - ${props.title}`}
          meta={{ name: 'BOA Holders', content: 'Lorem Ipsum' }}
        />
        <Container fluid="xl">
          <Row>
            <Col lg={12} md={12} sm={12}>
              {/* BOA Holders top section  */}
              <div className="top-header2">
                <div className="left"></div>
                <div className="right">
                  <GenericSearchBar
                    history={props.history}
                    searchedDataGet={(searchid: any) =>
                      dispatch(searchDataUpdater(searchid))
                    }
                    searchedData={searchedData}
                  />
                </div>
              </div>
            </Col>
          </Row>
          {/* BOA Holders Stats Component  */}
          <HoldersStatus />
          <div className="holder-list">
            {/* BOA holders list  */}
            <Table
              headerData={[
                {
                  No: "",
                  Address: "",
                  Balance: "",
                  Freezing: "",
                  Received_Rewards: "",
                  Percentage: "",
                  Value: "",
                  Analytics: "",
                }
              ]}
              headData={holdersList && holdersList.map((holder: any, index: number) => {
                return {
                  No: holder.index,
                  Address: (
                    <div className="copy-address">
                      <div
                        className="link-color address"
                        onClick={() => nodeDetails(holder.address)}
                      >
                        {holder.address.slice(0, 9) +
                          `…` +
                          holder.address.slice(-1 * 6)}
                      </div>
                    </div>
                  ),
                  Balance: (
                    <div className="values">
                      <span>{parseFloat(Amount(holder.total_balance)).toLocaleString('en-US', { maximumFractionDigits: 4, minimumFractionDigits: 4 })}</span>
                      <span>BOA</span>
                    </div>
                  ),
                  Freezing: (
                    <div className="values">
                      <span>{getPretty(Amount(holder.total_frozen))}</span>
                      <span>BOA</span>
                    </div>
                  ),
                  Received_Rewards: (
                    <div className="values">
                      <span>{parseFloat(Amount(holder.total_reward)).toLocaleString('en-US', { maximumFractionDigits: 4, minimumFractionDigits: 4 })}</span>
                      <span>BOA</span>
                    </div>
                  ),
                  Percentage: parseFloat(holder.percentage).toFixed(2) + "%",
                  Value: props.i18n.t("ChangeCurrency") + getPretty(holder.value),
                  Analytics: (
                    <div className="link-color" onClick={() => analyticChart(holder.address)}>
                      <img src={upchart} alt="upchart" width="100%" height="100%" />
                    </div>
                  ),
                }
              })}
              loading={loading}
              sortingData={(e) => sortingData(e)}
              sortingStatus={sortingStatus}
              type="Holder"
              CSVData={CSVData}
              pageSize={(pageSize: number) => dispatch(pageSizeChange(pageSize))}
              pageChange={(pageNumber: number) =>
                dispatch(pageChange(pageNumber))
              }
              showRecord={showRecord}
              currentPage={currentPage}
              pageCount={pageCount}
              props={props}
              data={holdersList}
              fileName={'Holders List.csv'}
              numberOfRecordShow={(Record: number) => numberOfRecordShow(Record)}
            />
          </div>
        </Container>
      </div>
    </div>
  )
}

export default withTranslation()(Holders)
