import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { pageChange } from 'store/pagination/thunks'
import { withTranslation } from 'react-i18next'
import endpoints from '../../../config/urlconfigs'
import { FaSortDown, FaSortUp } from 'react-icons/fa'
import { Button, Table } from 'components'
import CopyAddressClipboard from 'components/CopyAddressClipboard/CopyAddressClipboard'
import TooltipComp from 'components/TooltipComp/TooltipComp'
import request from '../../../api/request'
import { RouterPathEnum } from 'enums/RouterPathEnum'

interface hashHeightData {
  props: any
}
const EnrolledValidators: React.FC<hashHeightData> = (props: any) => {
  // All states for current screen
  const dispatch = useDispatch(),
    [showRecord, setTotalRecords] = useState<any>(20),
    [currentPage, setCurrentPage] = useState(1),
    [pageCount, setPageCount] = useState(0),
    [enrolledValidatorsData, setEnrolledValidatorsData] = useState([]),
    [openEnrolled, setOpenEnrolled] = useState(false),
    [CSVData, setCSVData] = useState([]),
    [loading, setLoading] = useState(true)

  const type = props.props.match.params['hash'].split(':')[0]
  const hash = props.props.match.params['hash'].split(':')[1]

  useEffect(() => {
    getEnrolledValidators()
  }, [showRecord, currentPage, hash, type])

  // Get Enrolled Validators API call
  const getEnrolledValidators = () => {
    request('GET', `${endpoints.enrolledValidators}?${type}=${hash}`, {
      pageSize: showRecord,
      page: currentPage,
    })
      .then((res: any) => {
        if (res.data.length > 0) {
          setOpenEnrolled(true)
        }
        setEnrolledValidatorsData(res.data)
        let CSVData: any = []
        setCSVData([])
        for (let i = 0; i < res.data.length; i++) {
          CSVData.push({
            UTXOKey: res.data[i].utxo,
            CycleLength: res.data[i].cycle_length,
            RandomSeed: res.data[i].commitment,
            EnrollSignature: res.data[i].enroll_sig,
          })
        }
        setCSVData(CSVData)
        setPageCount(res.data[0].full_count)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        return err
      })
  }

  const pagination = useSelector((state: any) => state.pagination)

  useEffect(() => {
    setCurrentPage(pagination.pageNumber)
  }, [pagination.pageNumber])

  // Show number of records in list function
  const numberOfRecordShow = (Record: Number) => {
    setTotalRecords(Record)
    setCurrentPage(1)
  }

  const transactionOverview = (utxoKey: string) => {
    props.props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${utxoKey}`, { utxoKey: true })
  }

  return (
    <>
      <div className="header">
        <h2>{props.i18n.t('Enrolled_Validators')}</h2>
        {/* Button to collapse Enrolled Validators list SHOW/HIDE */}
        <Button
          onClick={() => setOpenEnrolled(!openEnrolled)}
          aria-controls="example-fade-text"
          aria-expanded={openEnrolled}
        >
          {openEnrolled ? (
            <>
              {props.i18n.t('Hide_List')} <FaSortDown className="down" />
            </>
          ) : (
            <>
              {props.i18n.t('Show_List')} <FaSortUp className="up" />
            </>
          )}
        </Button>
      </div>
      {openEnrolled && (
        <div className="table-cont">
          {/* Enrolled Validators list */}
          <Table
            headerData={[
              {
                UTXO_Key: '',
                Cycle_Length: '',
                Random_Seed: '',
                Enroll_Signature: '',
              },
            ]}
            headData={enrolledValidatorsData.map(
              (singleValidator: any, index: number) => ({
                UTXO_Key: (
                  <div className="copy-address max_address">
                    <div onClick={() => transactionOverview(singleValidator.utxo)} className="link-color">
                      {singleValidator.utxo.slice(0, 6) +
                        `…` +
                        singleValidator.utxo.slice(-1 * 6)}
                    </div>
                    <CopyAddressClipboard
                      id={index}
                      text={singleValidator.utxo}
                    />
                  </div>
                ),
                Cycle_Length: (
                  <span className="cycleLength">
                    {singleValidator.cycle_length}
                  </span>
                ),
                Random_Seed: (
                  <TooltipComp
                    id={singleValidator.commitment}
                    text={singleValidator.commitment}
                  />
                ),
                Enroll_Signature: (
                  <TooltipComp
                    id={singleValidator.enroll_sig}
                    text={singleValidator.enroll_sig}
                  />
                ),
              }),
            )}
            loading={loading}
            currentPage={currentPage}
            pageCount={pageCount}
            pageChange={(pageNumber: number) =>
              dispatch(pageChange(pageNumber))
            }
            showRecord={showRecord}
            CSVData={CSVData}
            props={props}
            data={enrolledValidatorsData}
            fileName={'Enrolled Validators.csv'}
            numberOfRecordShow={(Record: number) => numberOfRecordShow(Record)}
          />
        </div>
      )}
    </>
  )
}

export default withTranslation()(EnrolledValidators)
