import React, { useEffect, useState } from 'react'
import { Container, Row, Col, Card, Tooltip } from 'reactstrap'
// import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
// import { RouterPathEnum } from 'enums/RouterPathEnum'
import { GenericSearchBar, PageHelmet, LineChart } from 'components'
import { searchDataUpdater } from 'store/header/thunks'
import SwiperCore, { A11y, Pagination, Scrollbar } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/components/pagination/pagination.scss'
import 'swiper/swiper.scss'
import { withTranslation } from 'react-i18next'
import endpoints from '../../config/urlconfigs'
import { Amount } from '../../config/config'
import request from '../../api/request'
import moment from 'moment'
import { Transaction, TxPayloadFee } from 'boa-sdk-ts'
import { UsePrevious } from "../../config/usePrevious";
import Loader from 'components/Loader/Loader'

var bigDecimal = require('js-big-decimal')

SwiperCore.use([Pagination, Scrollbar, A11y])

// const newdata: any = [
//   { time: 'Jan', price: 21, Perc: 5 },
//   { time: 'Feb', price: 32, Perc: 15 },
//   { time: 'Mar', price: 19, Perc: 11 },
//   { time: 'Apr', price: 8, Perc: 17 },
//   { time: 'May', price: 26, Perc: 5 },
//   { time: 'Jun', price: 31, Perc: 2 },
//   { time: 'Jul', price: 37, Perc: 28 },
//   { time: 'Aug', price: 16, Perc: 14 },
//   { time: 'Sep', price: 25, Perc: 19 },
// ]

// const selection = [
//   'Jan',
//   'Feb',
//   'Mar',
//   'Apr',
//   'May',
//   'Jun',
//   'Jul',
//   'Aug',
//   'Sep',
//   'Oct',
//   'Nov',
//   'Dec',
// ]

const FeeStatus: React.FC = (props: any) => {
  // const ref = React.useRef<any>(null);
  const [tooltipOpenData, setTooltipOpenData] = useState(false)
  const [tooltipOpenTotal, setTooltipOpenTotal] = useState(false)
  const toggleData = () => setTooltipOpenData(!tooltipOpenData)
  const toggleTotal = () => setTooltipOpenTotal(!tooltipOpenTotal)

  const dispatch = useDispatch()
  const [dataState, setDataState] = useState([])
  const [data, setData] = useState<any>({
    high: 0,
    medium: 0,
    low: 0,
    high_currency: 0,
    low_currency: 0,
    medium_currency: 0,
  })
  const [loading, setLoading] = useState(false)
  const searchedData = useSelector((state: any) => state.header.searchedData)
  const [time, setTime] = useState('D')
  const [outputs, setOutputs] = useState<any>(2)
  const [transactionSize, setTransactionSize] = useState<any>(364)
  const [payloadSize, setPayloadSize] = useState<any>('0')
  const [dataFee, setDataFee] = useState<any>('0')
  // const [outputsResponse, setOutputsResponse] = useState<any>('0')
  const [totalFee, setTotalFee] = useState<any>('0')
  const [error, setError] = useState('')
  // useEffect(() => {
  //   setLoading(true)
  //   const interval = setInterval(() => {
  //     setDataState((oldDataState: any) => [
  //       ...oldDataState,
  //       {
  //         time: selection[Math.floor(Math.random() * 11)],
  //         price: Math.floor(Math.random() * 50).toString(),
  //         Perc: Math.floor(Math.random() * 30).toString(),
  //       },
  //       setLoading(false)
  //     ]);
  //   }, 2000);

  //   return () =>
  //   setLoading(false)
  //   clearInterval(interval);
  // }, []);

  useEffect(() => {
    setLoading(true)
    request('GET', `${endpoints.transactionFees}/364?currency=${props.i18n.language === "korean" ? "krw" : props.i18n.language === "chinese" ? "cny" : "usd"}`, {})
      .then((res: any) => {
        setData(res.data)
      })
      .catch(() => { })

    request(
      'GET',
      `${endpoints.feeStatusChart}/?filter=${time}&date=${moment(new Date())
        .utc()
        .unix()}`,
      {},
    )
      .then((res: any) => {
        const chartData: any = []
        res.data && res.data.forEach((doc: any) =>
          chartData.push([doc.time_stamp * 1000, Amount(doc.average_tx_fee)]),
        )
        setDataState(chartData)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
      })
  }, [time, props.i18n.language])

  useEffect(
    () => {
      let isDown = false
      // let startX: any;
      // let scrollLeft: any;
      window.addEventListener('mousedown', (e: any) => {
        isDown = true
        // ref && ref.current.classList.add('active');
        // startX = e.pageX - ref.current.offsetLeft;
        // scrollLeft = ref.current.scrollLeft;
      })
      window.addEventListener('mouseleave', () => {
        isDown = false
        // ref.current.classList.remove('active');
      })
      window.addEventListener('mouseup', () => {
        isDown = false
        // ref.current.classList.remove('active');
      })
      window.addEventListener('mousemove', (e) => {
        if (!isDown) {
          return
        }
        e.preventDefault()
        // const x = e.pageX - ref.current.offsetLeft;
        // const walk = (x - startX) * 3;
        // ref.current.scrollLeft = scrollLeft - walk;
      })
    },
    // [ref]
  )

  const HorizontalSwiperParams = {
    // pagination: { clickable: true, dynamicBullets: true },
    breakpoints: {
      575: {
        slidesPerView: 2,
      },
      414: {
        slidesPerView: 1.5,
      },
      320: {
        slidesPerView: 1.3,
      },
    },
  }

  // const blockDetails = () => {
  //   props.history.push(RouterPathEnum.BLOCKSDETAILS)
  // }

  useEffect(() => {
    const res = Transaction.getEstimatedNumberOfBytes(2, outputs, 0)
    // const finalSize = bigDecimal.add(transactionSize, res)
    request('GET', `${endpoints.transactionFees}/${res}`, {})
      .then((res: any) => {
        setTotalFee(res.data.medium)
      })
      .catch((err) => {
        setError(err.response && err.response.data)
      })
  }, [])

  const onOutputsFocusOut = (value: any) => {
    // setOutputsResponse('0')
    if (value) {
      const res = Transaction.getEstimatedNumberOfBytes(2, value, 0)
      setTransactionSize(res)
      onTransactionSizeFocusOut(res)
      // setOutputsResponse(res)
    }
    else {
      setTransactionSize('')
      setTotalFee('0')
    }
  }

  const onPayloadSizeFocusOut = (value: any) => {
    setError('')
    setDataFee('0')
    try {
      const res = TxPayloadFee.getFee(value)
      setDataFee(res.toString())
    } catch (err: any) {
      setError(err.message)
    }
  }

  const previousAmount = UsePrevious(transactionSize);

  const onTransactionSizeFocusOut = (value: any) => {
    // const finalSize = bigDecimal.add(value, outputsResponse)
    if (value !== previousAmount) {
      setError('')
      setTotalFee('0')
      request('GET', `${endpoints.transactionFees}/${value}`, {})
        .then((res: any) => {
          setTotalFee(res.data.medium)
        })
        .catch((err) => {
          setError(err.response && err.response.data)
        })
    }
  }

  const UnitsFieldValidation = (evt: any) => {
    let regex = /^\d*$/.test(evt.key)
    if (evt.key === 'Backspace') {
      return false
    } else if (regex) {
      return false
    } else {
      return true
    }
  }

  const getPretty = (val: any) => {
    var value = bigDecimal.getPrettyValue(val)
    return value
  }

  const getTime = (value: any) => {
    var d = Math.floor(value / (3600 * 24));
    var h = Math.floor(value % (3600 * 24) / 3600);
    var m = Math.floor(value % 3600 / 60);
    var s = Math.floor(value % 60);

    var dDisplay = d > 0 ? d + (d === 1 ? "day " : "days ") : "";
    var hDisplay = h > 0 ? h + (h === 1 ? "hour " : "hours ") : "";
    var mDisplay = m > 0 ? m + (m === 1 ? "min " : "mins ") : "";
    var sDisplay = s > 0 ? s + (s === 1 ? "sec " : "secs ") : "";
    return dDisplay + hDisplay + mDisplay + sDisplay;
  }

  return (
    <div className="page-container">
      <div id="fee-status">
        <PageHelmet
          title={`BOSAGORA Explorer - ${props.title}`}
          meta={{ name: 'BOA FeeStatus', content: 'Lorem Ipsum' }}
        />
        <Container fluid="xl">
          <Row>
            <Col lg={12} md={12} sm={12}>
              <div className="top-header2">
                <div className="left"></div>
                <div className="right">
                  <GenericSearchBar
                    history={props.history}
                    searchedDataGet={(searchid: any) =>
                      dispatch(searchDataUpdater(searchid))
                    }
                    searchedData={searchedData}
                  />
                </div>
              </div>
            </Col>
          </Row>
          <Row>
            <Col lg={5} md={12} sm={12}>
              <h2>{props.i18n.t('Recommended_Transaction_Fees')}</h2>
            </Col>
            <Col lg={7} md={12} sm={12}>
              {/* Recommend fee Desktop view */}
              <div className="rec-desk">
                <div className="recommend-fee">
                  <p>
                    {moment
                      .utc(new Date())
                      .format('ddd, DD MMMM YYYY HH:mm:ss')}{' '}
                    UTC
                  </p>
                  <span>
                    {props.i18n.t('Current_recommended_Fee')}{' '}
                    {getPretty(Amount(data.medium))} BOAs per 364{' '}
                    {props.i18n.t('Bytes')}
                  </span>
                </div>
              </div>
            </Col>
          </Row>
          {/* Low, Medium , High Desktop view */}
          <div className="rec-desk">
            <div className="lmh-fee">
              <Card>
                <p>{props.i18n.t('Low')}</p>
                <h3 className="low">{getPretty(Amount(data.low))} BOA</h3>
                <p>{props.i18n.t('ChangeCurrency')} {data.low_currency} {data.low_delay && <>~ {getTime(data.low_delay)}</>}</p>
              </Card>
              <Card>
                <p>{props.i18n.t('Medium')}</p>
                <h3 className="medium">{getPretty(Amount(data.medium))} BOA</h3>
                <p>{props.i18n.t('ChangeCurrency')} {data.medium_currency} {data.medium_delay && <>~ {getTime(data.medium_delay)}</>}</p>
              </Card>
              <Card>
                <p>{props.i18n.t('High')}</p>
                <h3 className="high">{getPretty(Amount(data.high))} BOA</h3>
                <p>{props.i18n.t('ChangeCurrency')} {data.high_currency} {data.high_delay && <>~ {getTime(data.high_delay)}</>}</p>
              </Card>
            </div>
          </div>
          {/* Low, Medium , High Mobile Responsive view */}
          <div className="rec-res">
            <Swiper {...HorizontalSwiperParams} className="lmh-fee">
              <SwiperSlide>
                <Card>
                  <p>{props.i18n.t('Low')}</p>
                  <h3 className="low">{getPretty(Amount(data.low))} BOA</h3>
                  <p>{props.i18n.t('ChangeCurrency')} {data.low_currency} {data.low_delay && <>~ {getTime(data.low_delay)}</>}</p>
                </Card>
              </SwiperSlide>
              <SwiperSlide>
                <Card>
                  <p>{props.i18n.t('Medium')}</p>
                  <h3 className="medium">
                    {getPretty(Amount(data.medium))} BOA
                  </h3>
                  <p>{props.i18n.t('ChangeCurrency')} {data.medium_currency} {data.medium_delay && <>~ {getTime(data.medium_delay)}</>}</p>
                </Card>
              </SwiperSlide>
              <SwiperSlide>
                <Card>
                  <p>{props.i18n.t('High')}</p>
                  <h3 className="high">{getPretty(Amount(data.high))} BOA</h3>
                  <p>{props.i18n.t('ChangeCurrency')} {data.high_currency} {data.high_delay && <>~ {getTime(data.high_delay)}</>}</p>
                </Card>
              </SwiperSlide>
            </Swiper>
          </div>
          <Row>
            <Col lg={12} md={12} sm={12}>
              {/* Recommend fee Mobile Responsive view */}
              <div className="rec-res">
                <div className="recommend-fee">
                  <p>
                    {moment
                      .utc(new Date())
                      .format('ddd, DD MMMM YYYY HH:mm:ss')}{' '}
                    UTC
                  </p>
                  <span>
                    {props.i18n.t('Current_recommended_Fee')}{' '}
                    {getPretty(Amount(data.medium))} BOAs per 364{' '}
                    {props.i18n.t('Bytes')}
                  </span>
                </div>
              </div>
            </Col>
          </Row>
          <div className="feebox">
            {/* Fee Calculator Section  */}
            <div className="feeCalculator">
              <h2>{props.i18n.t('Fee_Calculator')}</h2>
              <div className="feeCalculatorcont">
                <div className="title">
                  {props.i18n.t('For_a_Standard_Transaction_with')}
                </div>
                {/* <div className="feeInputbox">
                  <p>Inputs</p>
                  <div className="input-field">
                    <input type="text" className="form-control" />
                  </div>
                </div> */}
                <div className="feeInputbox">
                  <p>{props.i18n.t('Outputs')}</p>
                  <div className="input-field">
                    <input
                      type="number"
                      placeholder="0"
                      min="0"
                      value={outputs}
                      onKeyDown={(evt: any) => {
                        UnitsFieldValidation(evt) && evt.preventDefault()
                        setOutputs(evt.target.value)
                      }}
                      // onBlur={() => onOutputsFocusOut()}
                      onChange={(e) => { setOutputs(e.target.value); onOutputsFocusOut(e.target.value) }}
                      className="form-control"
                    />
                  </div>
                </div>
                <div className="feeInputbox">
                  <p>{props.i18n.t('Transaction_size')}</p>
                  <div className="input-field">
                    <input
                      type="number"
                      placeholder="0"
                      min="0"
                      value={transactionSize}
                      onKeyDown={(evt: any) => {
                        UnitsFieldValidation(evt) && evt.preventDefault()
                        setTransactionSize(evt.target.value)
                      }}
                      // onBlur={() => onTransactionSizeFocusOut()}
                      onChange={(e) => { setTransactionSize(e.target.value); onTransactionSizeFocusOut(e.target.value) }}
                      className="form-control"
                    />
                    <span className="size">{props.i18n.t('Bytes')}</span>
                  </div>
                </div>
                <div className="feeInputbox">
                  <p>{props.i18n.t('Payload_size')}</p>
                  <div className="input-field">
                    <input
                      type="number"
                      min="0"
                      value={payloadSize}
                      placeholder="0"
                      onKeyDown={(evt: any) => {
                        UnitsFieldValidation(evt) && evt.preventDefault()
                        setPayloadSize(evt.target.value)
                      }}
                      // onBlur={() => onPayloadSizeFocusOut()}
                      onChange={(e) => { setPayloadSize(e.target.value); onPayloadSizeFocusOut(e.target.value) }}
                      className="form-control"
                    />
                    <span className="size">{props.i18n.t('Bytes')}</span>
                  </div>
                </div>
                <div className="feeInputbox mt-3 dt-desk">
                  <p>{props.i18n.t('Data_Fee')}</p>
                  <h3>
                    <span id="DataFee">{getPretty(Amount(dataFee))}</span>
                    <Tooltip
                      placement="top"
                      isOpen={tooltipOpenData}
                      target="DataFee"
                      toggle={toggleData}
                    >
                      {getPretty(Amount(dataFee))}
                    </Tooltip>
                    &nbsp;BOA
                  </h3>
                </div>
                <div className="feeInputbox dt-desk">
                  <p>{props.i18n.t('Total_Fee')}</p>
                  <h2>
                    <span id="TotalFee">
                      {getPretty(Amount(bigDecimal.add(dataFee, totalFee)))}
                    </span>
                    <Tooltip
                      placement="top"
                      isOpen={tooltipOpenTotal}
                      target="TotalFee"
                      toggle={toggleTotal}
                    >
                      {getPretty(Amount(bigDecimal.add(dataFee, totalFee)))}
                    </Tooltip>
                    &nbsp;BOA
                  </h2>
                </div>
                <div className="dt-res">
                  <div className="total-data">
                    <div className="data">
                      <p>{props.i18n.t('Data_Fee')}:</p>
                      <p>
                        <span>{getPretty(Amount(dataFee))}</span>&nbsp;BOA
                      </p>
                    </div>
                    <div className="total">
                      <p>{props.i18n.t('Total_Fee')}:</p>
                      <p>
                        <span>
                          {getPretty(Amount(bigDecimal.add(dataFee, totalFee)))}
                        </span>
                        &nbsp;BOA
                      </p>
                    </div>
                  </div>
                </div>
                <p className="errorMessage">{error}</p>
              </div>
            </div>
            {/* Fee Chart section  */}
            <div className="feeChart">
              <h2 className="feeChartH2">
                {props.i18n.t('Average_Transaction_Fee_Chart')}
              </h2>
              <div className="timeBtn">
                <button
                  className={time === 'D' ? 'active' : ''}
                  onClick={() => setTime('D')}
                >
                  1D
                </button>
                <button
                  className={time === '5D' ? 'active' : ''}
                  onClick={() => setTime('5D')}
                >
                  5D
                </button>
                <button
                  className={time === 'M' ? 'active' : ''}
                  onClick={() => setTime('M')}
                >
                  1M
                </button>
                <button
                  className={time === '3M' ? 'active' : ''}
                  onClick={() => setTime('3M')}
                >
                  3M
                </button>
                <button
                  className={time === '6M' ? 'active' : ''}
                  onClick={() => setTime('6M')}
                >
                  6M
                </button>
                <button
                  className={time === 'Y' ? 'active' : ''}
                  onClick={() => setTime('Y')}
                >
                  1Y
                </button>
                <button
                  className={time === '3Y' ? 'active' : ''}
                  onClick={() => setTime('3Y')}
                >
                  3Y
                </button>
                <button
                  className={time === '5Y' ? 'active' : ''}
                  onClick={() => setTime('5Y')}
                >
                  5Y
                </button>
              </div>
              <div className="chart">
                {loading ? (
                  <div className="chart-loader">
                    <Loader />
                  </div>
                ) : (
                  dataState.length > 0 ? (
                    <LineChart
                      status="feeStatusChart"
                      data={[
                        {
                          name: '',
                          data: dataState,
                        },
                      ]}
                    />
                  ) : (
                    <div className="NoRecordChart">
                      <h4>{props.i18n.t('NoRecord')}</h4>
                    </div>
                  ))}
              </div>
            </div>
          </div>
        </Container>
      </div>
    </div>
  )
}

export default withTranslation()(FeeStatus)
