import React, { useEffect, useState } from 'react'
import { Container, Row, Col, Card } from 'reactstrap'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { AiFillCaretUp, AiFillCaretDown } from 'react-icons/ai'
import BOA from 'images/Uni.svg'
import height from 'images/height.svg'
import transactionIcon from 'images/transaction.svg'
import frozen from 'images/frozen.svg'
import validator from 'images/validator.svg'
import active_val from 'images/active_val.svg'
import circulate from 'images/circulate.svg'
import { ReactSVG } from 'react-svg'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import { withTranslation, WithTranslation } from 'react-i18next'
import socketIOClient from 'socket.io-client'
import endpoints from '../../config/urlconfigs'
import request from '../../api/request'
import {
  PageHelmet,
  SearchBar,
  LineChart,
} from 'components'
import {
  searchDataUpdater,
  headerClassUpdater,
  getAllBlocksonDashboard,
  getAllTransactionsonDashboard,
  allBoaStatsforDashboard,
  coinMarketCap,
} from 'store/header/thunks'
import moment from 'moment'
import 'moment/locale/ko';
import 'moment/locale/zh-cn';
import 'moment/locale/en-gb';
import Loader from 'components/Loader/Loader'
import { Amount } from 'config/config'
import {fixToThree, getPretty} from "../../utils/CalcUtils";

interface IMyProps extends WithTranslation {
  store?: any
  data?: any
  loading?: any
  title?: string
  output: any
  name?: any
  history?: any
  searchedData: {
    searchType: ''
    available: false
    details: {}
  }
  blocksData: any
  transactionsData: any
}

interface AllBoaStats {
  active_validators: number
  circulating_supply: number
  frozen_coin: number
  height: number
  transactions: number
  validators: number
  time_stamp: number
}

interface MarketCapStats {
  last_updated_at: any
  market_cap: number
  change_24h: number
  price: number
  time: any
  vol_24h: number
}

const Dashboard: React.FC<IMyProps> = (props: IMyProps) => {
  // states for current screen
  const dispatch = useDispatch()
  const allBoaStatVals: AllBoaStats = useSelector(
      (state: any) => state.header.boaStats,
  )

  const marketCapStats: MarketCapStats = useSelector(
      (state: any) => state.header.marketCapStats,
  )

  const searchedData = useSelector((state: any) => state.header.searchedData)
  const isCoinNet = useSelector((state: any) => state.header.isCoinNet)

  const [dataState, setDataState] = useState([])
  const [loading, setLoading] = useState(false)
  const [chartLoading, setChartLoading] = useState(false)

  const [transaction, setTransaction] = useState<any>([])
  const [blocks, setBlocks] = useState<any>([])
  const [stats, setStats] = useState<any>('0')
  const [currentTime, setCurrentTime] = useState(new Date())
  // const [blocksDataNew, setBlockDataNew] = useState<any>('')
  // const [newBlock, setnewBlock] = useState<any>('')
  const [blocksData, setBlocksData] = useState<any>([])
  const [transactionsData, setTransactionsData] = useState<any>([])

  // All APIs Call functions
  useEffect(() => {
    setLoading(true)
    setChartLoading(true)
    request('GET', `${endpoints.latestBlocks}`, {})
        .then((res: any) => {
          setBlocksData(res.data)
          setLoading(false)
        })
        .catch((err: any) => {
          setLoading(false)
          return err
        })

    request('GET', `${endpoints.chart}?currency=${props.i18n.language === "korean" ? "krw" : props.i18n.language === "chinese" ? "cny" : "usd"}`, {})
        .then((res: any) => {
          const chartData: any = []
          res.data.forEach((doc: any) => {
            chartData.push([doc.last_updated_at * 1000, doc.usd_price])
          })
          setDataState(chartData)
          setChartLoading(false)
        })
        .catch((err: any) => {
          setChartLoading(false)
          return err
        })

    request('GET', `${endpoints.latestTransactions}`, {})
        .then((res: any) => {
          setTransactionsData(res.data)
          setLoading(false)
        })
        .catch((err: any) => {
          setLoading(false)
          return err
        })
    const allDataGetter = async (page: number, limit: number) => {
      dispatch(allBoaStatsforDashboard())
      dispatch(coinMarketCap(props.i18n.language))
      dispatch(getAllBlocksonDashboard(page, limit))
      dispatch(getAllTransactionsonDashboard(page, limit))
    }
    allDataGetter(1, 11)
  }, [dispatch])

  useEffect(() => {
    setChartLoading(true)
    request('GET', `${endpoints.chart}?currency=${props.i18n.language === "korean" ? "krw" : props.i18n.language === "chinese" ? "cny" : "usd"}`, {})
        .then((res: any) => {
          const chartData: any = []
          res.data.forEach((doc: any) => {
            chartData.push([doc.last_updated_at * 1000, doc.usd_price])
          })
          setDataState(chartData)
          setChartLoading(false)
        })
        .catch((err: any) => {
          setChartLoading(false)
        })

    dispatch(coinMarketCap(props.i18n.language))
  }, [dispatch, props.i18n.language])

  // All Sockets call function
  useEffect(() => {
    setInterval(() => {
      setCurrentTime(new Date())
    }, 1000)
    const socket = socketIOClient(endpoints.api)
    socket.on('newtransaction', (data) => {
      if (data) {
        let latestTransaction = []
        setTransaction([])
        for (let i = 0; i < data.length; i++) {
          latestTransaction.push({
            amount: '',
            full_count: 0,
            height: data[i].height,
            time_stamp: data[i].time_stamp,
            tx_fee: '',
            tx_hash: data[i].tx_hash,
            tx_size: '',
            type:
                data[i].transaction.outputs[0].type === 0
                    ? props.i18n.t('Payment')
                    : data[i].transaction.outputs[0].type === 1
                        ? props.i18n.t('Freeze')
                        : data[i].transaction.outputs[0].type === 2
                            ? props.i18n.t('Coinbase')
                            : data[i].transaction.outputs[0].type,
          })
        }
        setTransaction(latestTransaction)
      }
    })
    const socketBlock = socketIOClient(endpoints.api)
    socketBlock.on('newblock', (data) => {
      if (data) {
        let latestBlock = [
          {
            enrollment_count: data.block.header.enrollments.length.toString(),
            full_count: parseInt(data.height) + 1,
            hash: data.hash,
            height: data.height,
            merkle_root: data.block.header.merkle_root,
            signature: data.block.header.signature,
            time_stamp: data.time_stamp,
            tx_count: data.block.txs.length.toString(),
            validators: data.block.header.validators,
          },
        ]
        setBlocks(latestBlock)
      }
    })
    const socketStats = socketIOClient(endpoints.api)
    socketStats.on('lateststats', (data) => {
      setStats(data)
    })
  }, [])
  useEffect(() => {
    if (blocks) {
      let blockNewData = blocks.concat(blocksData)
      setBlocksData(blockNewData)
      if (blockNewData.length > 10) {
        blockNewData.pop()
      }
    }
  }, [blocks])

  useEffect(() => {
    if (transaction) {
      let transactionNewData = transaction.concat(transactionsData)
      setTransactionsData(transactionNewData)
      if (transactionNewData.length > 10) {
        transactionNewData.pop()
      }
      // let tx = transaction.tx
      // if (tx) {
      //   tx.map((item: any, index: any) => item)
      //   Object.assign({}, transaction.tx)
      //   let transactionNewData = transaction.tx.concat(transactionsData)
      //   transactionNewData.length = 10
      //   setTransactionsData(transactionNewData)
      // }
    }
  }, [transaction])

  const changeRoute = (route: string) => {
    props.history.push(route)
    dispatch(headerClassUpdater('newclass2', 'layout-generic'))
  }

  const transactionOverview = (hash: string) => {
    props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${hash}`)
  }

  const getLatestHeightTime = (time: any) => {
    let t1 = currentTime;
    let t2 = new Date(time * 1000);
    let dif = (t1.getTime() - t2.getTime()) / 1000;

    let d = Math.floor(dif / (3600 * 24));
    let h = Math.floor(dif % (3600 * 24) / 3600);
    let m = Math.floor(dif % 3600 / 60);
    let s = Math.floor(dif % 60);

    let dDisplay = d > 0 ? d + (d === 1 ? "day " : "days ") : " ";
    let hDisplay = h > 0 ? h + (h === 1 ? "hour " : "hours ") : " ";
    let mDisplay = m > 0 ? m + (m === 1 ? "min " : "mins ") : " ";
    let sDisplay = s > 0 ? s + (s === 1 ? "sec " : "secs ") : "0sec ";
    return props.i18n.t("Last") + " " + dDisplay + hDisplay + mDisplay + sDisplay + props.i18n.t("Ago");
  }

  const getBlockTime = () => {
    const res = blocksData.sort((a: any, b: any) => a.count - b.count).slice(0, 2).map((res: any) => res.time_stamp)
    if (res.length) {
      let t1 = new Date(res[0] * 1000);
      let t2 = new Date(res[1] * 1000);
      let dif = (t1.getTime() - t2.getTime()) / 1000;

      let d = Math.floor(dif / (3600 * 24));
      let h = Math.floor(dif % (3600 * 24) / 3600);
      let m = Math.floor(dif % 3600 / 60);
      let s = Math.floor(dif % 60);

      let dDisplay = d > 0 ? d + (d === 1 ? "day " : "days ") : "";
      let hDisplay = h > 0 ? h + (h === 1 ? "hour " : "hours ") : "";
      let mDisplay = m > 0 ? m + (m === 1 ? "min " : "mins ") : "";
      let sDisplay = s > 0 ? s + (s === 1 ? "sec " : "secs ") : "";
      return dDisplay + hDisplay + mDisplay + sDisplay;
    }
    else {
      return 0 + "sec";
    }
  }


  return (
      <div id="dashboard">
        <PageHelmet
            title={`BOSAGORA Explorer`}
            meta={{ name: 'Dashboard', content: 'Lorem Ipsum' }}
        />
        {/* Dashboard Top section  */}
        <div className="dashboard-top">
          <div className="bg"/>
          <Container fluid="xl">
            <Row>
              <Col xl={12} lg={12}>
                <h1>{props.i18n.t('Welcome_to_BOSAGORA_Explorer')}</h1>
                <SearchBar
                    history={props.history}
                    searchedDataGet={(searchid: any) =>
                        dispatch(searchDataUpdater(searchid))
                    }
                    searchedData={searchedData}
                />
              </Col>
            </Row>
          </Container>
        </div>
        <div className="dashboard">
          {/* Coin Market Cap and Chart section  */}
          <Container fluid="xl">
            <div className="coinmarket-chart">
              <div className="coin-market">
                <div className="coin-blocktime">
                  <div className="icon-coin">
                    <img src={BOA} alt="Coin" width="100%" height="100%" />
                    <h4>BOA</h4>
                  </div>
                  <div className="d-sm">
                    <div className="main-val">
                      <h4>
                        {props.i18n.t('ChangeCurrency')}{' '}
                        {getPretty(fixToThree(marketCapStats.price))}
                      </h4>
                    </div>
                  </div>
                </div>
                <div className="main-val d-lg">
                  <h4>
                    {props.i18n.t('ChangeCurrency')}{' '}
                    {getPretty(fixToThree(marketCapStats.price))}
                  </h4>
                </div>
                <div className="marketcap-value">
                  <div className="coin-marektcap">
                    <p>
                      {props.i18n.t('Block_Time')} <b>{getBlockTime()}</b>
                      {/* <b>{moment.unix(marketCapStats.last_updated_at)}</b> */}
                    </p>
                  </div>
                  <div className="coin-marketval">
                    {marketCapStats.change_24h > 0 ? <AiFillCaretUp className="upIcon" /> : <AiFillCaretDown className="downIcon" />}
                    {/* <AiFillCaretDown/> */}
                    <p>
                      {/* + */}
                      {getPretty(marketCapStats.change_24h)}% (24h)
                    </p>
                  </div>
                </div>
                <div className="marketcap-vol">
                  <div className="item">
                    <p>{props.i18n.t('Market_Cap')}</p>
                    <p className="value">
                      {' '}
                      {props.i18n.t('ChangeCurrency')}{' '}
                      {getPretty(marketCapStats.market_cap)}
                    </p>
                  </div>
                  <div className="item">
                    <p>{props.i18n.t('Vol')}</p>
                    <p className="value">
                      {' '}
                      {props.i18n.t('ChangeCurrency')}{' '}
                      {getPretty(marketCapStats.vol_24h)}
                      {/* {marketCapStats.vol_24h} */}
                    </p>
                  </div>
                </div>
                {/* Buttons for Desktop View  */}
                <div className="d-lg">
                  <div className="btns">
                    {/* <Button>{props.i18n.t('Web_Wallet')}</Button> */}
                    <a
                        className="btn"
                        href={isCoinNet?window.location.protocol+"//coinnet.boawallet.io":window.location.protocol+"//testnet.boawallet.io"}
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                      {props.i18n.t('Web_Wallet')}
                    </a>
                    <a
                        className="btn"
                        href="/#"
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                      Votera
                    </a>
                    {/* <Button>Votera</Button> */}
                  </div>
                </div>
              </div>
              {/* BOA Stats Mobile Responsive View  */}
              <div className="d-sm">
                <div className="market-items">
                  <Card
                      onClick={() =>
                          changeRoute(
                              `${RouterPathEnum.BLOCKSDETAILS}/height:${allBoaStatVals.height}`,
                          )
                      }
                  >
                    <div className="icon">
                      <ReactSVG src={height} />
                    </div>
                    <div className="values">
                      <div className="title">
                        <h4>{props.i18n.t('Height')}</h4>
                      </div>
                      <h4>
                        {stats.height
                            ? getPretty(stats.height)
                            : getPretty(allBoaStatVals.height)}
                      </h4>
                      <p>{getLatestHeightTime(stats.time_stamp ? stats.time_stamp : allBoaStatVals.time_stamp)
                        // ? moment.utc(stats.time_stamp * 1000).locale(props.i18n.language === "korean" ? "ko" : props.i18n.language === "chinese" ? "zh-cn" : "en").fromNow() : moment.utc(allBoaStatVals.time_stamp * 1000).locale(props.i18n.language === "korean" ? "ko" : props.i18n.language === "chinese" ? "zh-cn" : "en").fromNow()
                      }</p>
                    </div>
                  </Card>
                  <Card onClick={() => changeRoute(RouterPathEnum.HOLDERS)}>
                    <div className="icon">
                      <ReactSVG src={frozen} />
                    </div>
                    <div className="values">
                      <div className="title">
                        <h4>{props.i18n.t('Frozen_Coin')}</h4>
                      </div>
                      <h4>{getPretty(Amount(allBoaStatVals.frozen_coin))}</h4>
                      <p>BOA</p>
                    </div>
                  </Card>
                  <Card onClick={() => changeRoute(RouterPathEnum.HOLDERS)}>
                    <div className="icon">
                      <ReactSVG src={circulate} />
                    </div>
                    <div className="values">
                      <div className="title">
                        <h4>{props.i18n.t('Circulating_Supply')}</h4>
                      </div>
                      <h4>
                        {stats.circulating_supply
                            ? getPretty(Amount(stats.circulating_supply))
                            : getPretty(Amount(allBoaStatVals.circulating_supply))}
                      </h4>
                      <p>BOA</p>
                    </div>
                  </Card>
                </div>
              </div>
              <div className="chart">
                <div className="d-sm">
                  <h4>{props.i18n.t('BOA_Price_Chart')}</h4>
                </div>
                {chartLoading ? (
                    <div className="chart-loader">
                      <Loader />
                    </div>
                ) : (
                    dataState.length > 0 ? (
                        <LineChart
                            status="dashboardChart"
                            data={[
                              {
                                name: '',
                                data: dataState,
                              },
                            ]}
                        />
                    ) : (
                        <div className="NoRecordChart">
                          <h4>{props.i18n.t('NoRecord')}</h4>
                        </div>
                    ))}
              </div>
              {/* Button for Mobile Responsive View  */}
              <div className="d-sm">
                <div className="btns">
                  <a
                      className="btn"
                      href="https://dev-www-wallet.bosagora.com/"
                      target="_blank"
                      rel="noopener noreferrer"
                  >
                    {props.i18n.t('Web_Wallet')}
                  </a>
                  <a
                      className="btn"
                      href="/#"
                      target="_blank"
                      rel="noopener noreferrer"
                  >
                    Votera
                  </a>
                  {/* <Button>{props.i18n.t('Web_Wallet')}</Button> */}
                  {/* <Button>Votera</Button> */}
                </div>
              </div>
            </div>
            {/* BOA Stats for Desktop View  */}
            <div className="d-lg">
              <div className="market-values">
                <Card>
                  <div className="head">
                    <div
                        className="title"
                        onClick={() =>
                            changeRoute(
                                `${RouterPathEnum.BLOCKSDETAILS}/height:${stats.height ? stats.height : allBoaStatVals.height}`,
                            )
                        }
                    >
                      <ReactSVG src={height} />
                      <p>{props.i18n.t('Height')}</p>
                    </div>
                  </div>
                  <div className="values">
                    <h4>
                      {stats.height
                          ? getPretty(stats.height)
                          : getPretty(allBoaStatVals.height)}
                    </h4>
                    <p>{getLatestHeightTime(stats.time_stamp ? stats.time_stamp : allBoaStatVals.time_stamp)
                      // ? moment.utc(stats.time_stamp * 1000).locale(props.i18n.language === "korean" ? "ko" : props.i18n.language === "chinese" ? "zh-cn" : "en").fromNow() : moment.utc(allBoaStatVals.time_stamp * 1000).locale(props.i18n.language === "korean" ? "ko" : props.i18n.language === "chinese" ? "zh-cn" : "en").fromNow()
                    }</p>
                  </div>
                </Card>
                <Card>
                  <div className="head">
                    <div
                        className="title"
                        onClick={() => changeRoute(RouterPathEnum.TRANSACTIONS)}
                    >
                      <ReactSVG src={transactionIcon} />
                      <p>{props.i18n.t('Transactions')}</p>
                    </div>
                  </div>
                  <div className="values">
                    <h4>
                      {stats.transactions
                          ? getPretty(stats.transactions)
                          : getPretty(allBoaStatVals.transactions)}
                    </h4>
                    <p>{props.i18n.t('Total')}</p>
                  </div>
                </Card>
                <Card>
                  <div className="head">
                    <div
                        className="title"
                        onClick={() => changeRoute(RouterPathEnum.HOLDERS)}
                    >
                      <ReactSVG src={frozen} />
                      <p>{props.i18n.t('Frozen_Coin')}</p>
                    </div>
                  </div>
                  <div className="values">
                    <h4>
                      {stats.frozen_coin
                          ? getPretty(Amount(stats.frozen_coin))
                          : getPretty(Amount(allBoaStatVals.frozen_coin))}
                    </h4>
                    <p>BOA</p>
                  </div>
                </Card>
                <Card>
                  <div className="head">
                    <div
                        className="title"
                        onClick={() => changeRoute(RouterPathEnum.HOLDERS)}
                    >
                      <ReactSVG src={circulate} />
                      <p>{props.i18n.t('Circulating_Supply')}</p>
                    </div>
                  </div>
                  <div className="values">
                    <h4>
                      {getPretty(Amount(allBoaStatVals.circulating_supply))}
                    </h4>
                    <p>BOA</p>
                  </div>
                </Card>
                <Card>
                  <div className="head">
                    <div
                        className="title"
                        onClick={() => changeRoute(RouterPathEnum.NODES)}
                    >
                      <ReactSVG src={validator} />
                      <p>{props.i18n.t('Number_of_Validators')}</p>
                    </div>
                  </div>
                  <div className="values">
                    <h4>
                      {stats.validators
                          ? getPretty(stats.validators)
                          : getPretty(allBoaStatVals.validators)}
                    </h4>
                    <p>{props.i18n.t('Total')}</p>
                  </div>
                </Card>
                <Card>
                  <div className="head">
                    <div
                        className="title"
                        onClick={() => props.history.push(`${RouterPathEnum.BLOCKSDETAILS}/height:${stats.height ? stats.height : allBoaStatVals.height}`, { validatorsSection: true })}
                    >
                      <ReactSVG src={active_val} />
                      <p>{props.i18n.t('Active_Validators')}</p>
                    </div>
                  </div>
                  <div className="values">
                    <h4>
                      {stats.active_validators
                          ? getPretty(stats.active_validators)
                          : getPretty(allBoaStatVals.active_validators)}
                    </h4>
                    <p>{props.i18n.t('Total')}</p>
                  </div>
                </Card>
              </div>
            </div>
          </Container>
          <Container fluid="xl">
            <div className="bt-container">
              {/* Top Blocks section */}
              <div className="blocks">
                <div className="bt-top">
                  <h4>{props.i18n.t('Blocks')}</h4>
                </div>
                <div className="table-responsive">
                  <table>
                    <thead>
                    <tr>
                      <th>{props.i18n.t('Height')}</th>
                      <th>{props.i18n.t('Timestamp')}</th>
                      <th>{props.i18n.t('Transactions')}</th>
                    </tr>
                    </thead>
                    <tbody>
                    {loading ? (
                        <tr className="no-data">
                          <td className="w-100">
                            <Loader />
                          </td>
                        </tr>
                    ) : blocksData.length === 0 ? (
                        <tr className="no-data">
                          <td className="w-100">
                            <h4>{props.i18n.t('NoRecord')}</h4>
                          </td>
                        </tr>
                    ) : (
                        <>
                          {blocksData.slice(0, 10).map((item: any, index: number) => (
                              <tr key={index}>
                                <td>
                                  <div
                                      className="link-color"
                                      onClick={() =>
                                          changeRoute(
                                              `${RouterPathEnum.BLOCKSDETAILS}/height:${item.height}`,
                                          )
                                      }
                                  >
                                    {getPretty(item.height)}
                                  </div>
                                </td>
                                <td>{(moment.utc(item.time_stamp * 1000).fromNow().includes("day") || moment.utc(item.time_stamp * 1000).fromNow().includes("month") || moment.utc(item.time_stamp * 1000).fromNow().includes("year")) ? moment.utc(item.time_stamp * 1000).format("YYYY-MM-DD HH:mm:ss") + " UTC" : moment.utc(item.time_stamp * 1000).locale(props.i18n.language === "korean" ? "ko" : props.i18n.language === "chinese" ? "zh-cn" : "en").fromNow()}</td>
                                <td>{item.tx_count}</td>
                              </tr>
                          ))}
                        </>
                    )}
                    </tbody>
                  </table>
                </div>
                <div className="bt-bottom">
                  <Link to="/blocks">{props.i18n.t('View_All_Blocks')}</Link>
                </div>
              </div>
              {/* Top Transactions section */}
              <div className="transactions">
                <div className="bt-top">
                  <h4>{props.i18n.t('Transactions')}</h4>
                </div>
                <div className="table-responsive">
                  <table>
                    <thead>
                    <tr>
                      <th>{props.i18n.t('Tx_Hash')}</th>
                      <th>{props.i18n.t('Type')}</th>
                      <th>{props.i18n.t('Height')}</th>
                      <th>{props.i18n.t('Time')}</th>
                    </tr>
                    </thead>
                    <tbody>
                    {loading ? (
                        <tr className="no-data">
                          <td className="w-100">
                            <Loader />
                          </td>
                        </tr>
                    ) : transactionsData.length === 0 ? (
                        <tr className="no-data">
                          <td className="w-100">
                            <h4>{props.i18n.t('NoRecord')}</h4>
                          </td>
                        </tr>
                    ) : (
                        <>
                          {transactionsData.slice(0, 10).map((item: any, index: any) => (
                              <tr key={index}>
                                <td>
                                  <div
                                      className="link-color"
                                      onClick={() =>
                                          transactionOverview(item.tx_hash)
                                      }
                                  >
                                    {item.tx_hash.slice(0, 6) +
                                        `…` +
                                        item.tx_hash.slice(-1 * 6)}
                                  </div>
                                </td>
                                <td>{props.i18n.t(item.type)}</td>
                                <td>{item.height ? getPretty(item.height) : item.height}</td>
                                <td>{(moment.utc(item.time_stamp * 1000).fromNow().includes("day") || moment.utc(item.time_stamp * 1000).fromNow().includes("month") || moment.utc(item.time_stamp * 1000).fromNow().includes("year")) ? moment.utc(item.time_stamp * 1000).format("YYYY-MM-DD HH:mm:ss") + " UTC" : moment.utc(item.time_stamp * 1000).locale(props.i18n.language === "korean" ? "ko" : props.i18n.language === "chinese" ? "zh-cn" : "en").fromNow()}</td>
                              </tr>
                          ))}
                        </>
                    )}
                    </tbody>
                  </table>
                </div>
                <div className="bt-bottom">
                  <Link to="/transactions">
                    {props.i18n.t('View_All_Transactions')}
                  </Link>
                </div>
              </div>
            </div>
          </Container>
        </div>
      </div>
  )
}

export default withTranslation()(Dashboard)
