import React, { useState, useEffect } from 'react'
import { Container, Row, Col } from 'reactstrap'
import { useDispatch, useSelector } from 'react-redux'
// import { ReactSVG } from 'react-svg'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import { pageChange } from 'store/pagination/thunks'
import { GenericSearchBar, PageHelmet, Table } from 'components'
import {
  searchDataUpdater,
  // getAllTransactionsonTransactionsDetails,
} from 'store/header/thunks'
// import copy from 'images/copy.svg'
import { withTranslation } from 'react-i18next'
import request from '../../api/request'
import CopyAddressClipboard from 'components/CopyAddressClipboard/CopyAddressClipboard'
import endpoints from '../../config/urlconfigs'
import { Amount } from 'config/config'

var bigDecimal = require('js-big-decimal')
// import { login } from "../../store/auth/thunks";

const getPretty = (val: any) => {
  var value = bigDecimal.getPrettyValue(val)
  return value
}

// interface IMyProps {
//   store?: any
//   data?: any
//   title?: string
//   output: any
//   name?: any
//   history?: any
//   searchedDataGet: Function
//   searchDataCleaner: Function
//   searchedData: {
//     searchType: ''
//     available: false
//     details: {}
//   }
// }

const Nodes: React.FC = (props: any) => {
  // All states for current screen
  const dispatch = useDispatch()
  const [currentPage, setCurrentPage] = useState(1)
  const [showRecord, setTotalRecords] = useState<any>(20)
  const [pageCount, setPageCount] = useState(0)
  const [loading, setLoading] = useState(false)
  const searchedData = useSelector((state: any) => state.header.searchedData)
  const pagination = useSelector((state: any) => state.pagination)
  const [data, setData] = useState<any>([])
  const [CSVData, setCSVData] = useState([]);
  // useEffect(() => {
  //   let dataSet = [...Array(Math.ceil(25 + Math.random() * 25))].map(
  //     (a, i) => 'Record ' + (i + 1),
  //   )
  //   let pageSize = 10
  //   let pageCount = Math.ceil(dataSet.length / pageSize)
  //   setPageCount(pageCount)
  // }, [])

  useEffect(() => {
    setCurrentPage(pagination.pageNumber)
    // dispatch(getAllTransactionsonTransactionsDetails(pagination.pageNumber, 10))
  }, [pagination.pageNumber, dispatch])

  const nodeDetails = (address: any) => {
    props.history.push(`${RouterPathEnum.NODEDETAILS}/${address}`)
  }
  // Redirect to other screen function 
  // const transactionOverview = (hash: string) => {
  //   props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${hash}`)
  // }

  let numberIndex = 0
  let numberIndex2 = 0

  // useEffect(() => {
  //   getNodes()
  // }, [])

  numberIndex = showRecord * (currentPage === 0 ? 0 : (currentPage) - 1)
  numberIndex2 = showRecord * (currentPage === 0 ? 0 : (currentPage) - 1)
  useEffect(() => {
    getNodes()
  }, [showRecord, currentPage])

  // Get latest blocks API call 
  const getNodes = () => {
    setLoading(true)
    request('GET', `${endpoints.validators}`, { pageSize: showRecord, page: currentPage })
      .then((res: any) => {
        setData(res.data)
        let CSVData: any = []
        setCSVData([])
        for (let i = 0; i < res.data.length; i++) {
          numberIndex2 = numberIndex2 + 1
          CSVData.push({
            No: getPretty(numberIndex2),
            UTXOKey: res.data[i].stake,
            PublicKey: res.data[i].address,
            AgoraVersion: res.data[i].agora_version,
            Freezing: res.data[i].stake_amount ? getPretty(Amount(res.data[i].stake_amount)) + " BOA" : getPretty(res.data[i].stake_amount) + " BOA",
            EnrolledHeight: getPretty(res.data[i].enrolled_at),
            PreimageHeight: getPretty(res.data[i].preimage.height),
            CurrentBlockHeight: getPretty(res.data[i].block_height),
            Validator: res.data[i].slashed === 1 ? props.i18n.t('No') : props.i18n.t('Yes'),
            Slashing: res.data[i].slashed === 1 ? props.i18n.t('Yes') : props.i18n.t('No'),
          })
        }
        setCSVData(CSVData)
        setPageCount(res.data[0].full_count)
        setLoading(false)
      })
      .catch((err: any) => {
        setLoading(false)
        return err
      })
  }

  const numberOfRecordShow = (Record: Number) => {
    setTotalRecords(Record)
    setCurrentPage(1)
  }

  const transactionOverview = (utxoKey: string) => {
    props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${utxoKey}`, { utxoKey: true })
  }

  return (
    <div className="page-container">
      <div id="nodes">
        <PageHelmet
          title={`BOSAGORA Explorer - ${props.title}`}
          meta={{ name: 'Nodes', content: 'BOSAGORA Explorer - Nodes' }}
        />
        <Container fluid="xl">
          <Row>
            <Col lg={12} md={12} sm={12}>
              {/* Nodes top section  */}
              <div className="top-header2">
                <div className="left"></div>
                <div className="right">
                  <GenericSearchBar
                    history={props.history}
                    searchedDataGet={(searchid: any) =>
                      dispatch(searchDataUpdater(searchid))
                    }
                    searchedData={searchedData}
                  />
                </div>
              </div>
            </Col>
          </Row>
          <Row>
            <Col lg={12} md={12} sm={12}>
              <h2>{props.i18n.t('Nodes')}</h2>
            </Col>
          </Row>
          {/* Nodes list */}
          <Table
            headerData={[
              {
                No: "",
                UTXO_Key: "",
                Public_Key: "",
                Agora_Version: "",
                Freezing: "",
                Enrolled_Height: "",
                Preimage_Height: "",
                Current_Block_Height: "",
                Validator: "",
                Slashing: "",
              }]}
            headData={data.map((node: any, index: number) => {
              numberIndex = numberIndex + 1
              return {
                No: getPretty(numberIndex),
                UTXO_Key: (
                  <div className="copy-address">
                    <div onClick={() => transactionOverview(node.stake)} className="link-color">
                      {node.stake.slice(0, 6) +
                        `…` +
                        node.stake.slice(-1 * 6)}
                    </div>
                    {/* <CopyAddressClipboard text={node.stake} /> */}
                  </div>
                ),
                Public_Key: (
                  <div className="copy-address max_address">
                    <div onClick={() => nodeDetails(node.address)} className="link-color">
                      {node.address.slice(0, 6) +
                        `…` +
                        node.address.slice(-1 * 6)}
                    </div>
                    <CopyAddressClipboard id={index} text={node.address} />
                  </div>
                ),
                Agora_Version: node.agora_version,
                Freezing: <div>{node.stake_amount ? getPretty(Amount(node.stake_amount)) : getPretty(node.stake_amount)} BOA</div>,
                Enrolled_Height: (
                  <div>{getPretty(node.enrolled_at)}</div>
                ),
                Preimage_Height: <div>{getPretty(node.preimage.height)}</div>,
                Current_Block_Height: <div>{node.block_height ? getPretty(node.block_height) : ""}</div>,
                Validator: node.slashed === 1 ? props.i18n.t('No') : props.i18n.t('Yes'),
                Slashing: node.slashed === 1 ? props.i18n.t('Yes') : props.i18n.t('No'),
              }
            })}
            loading={loading}
            type="Node"
            currentPage={currentPage}
            pageCount={pageCount}
            pageChange={(pageNumber: number) =>
              dispatch(pageChange(pageNumber))
            }
            showRecord={showRecord}
            props={props}
            data={data}
            CSVData={CSVData}
            fileName={'Nodes List.csv'}
            numberOfRecordShow={(Record: number) => numberOfRecordShow(Record)}
          />
        </Container>
      </div>
    </div>
  )
}

export default withTranslation()(Nodes)
