import React, { useState, useEffect } from 'react'
import { Row, Col, Card } from 'reactstrap'
import { ReactSVG } from 'react-svg'
import price from 'images/price.svg'
import frozen from 'images/frozen.svg'
import circulate from 'images/circulate.svg'
import rewards from 'images/rewards.svg'
import { withTranslation } from 'react-i18next'
import endpoints from '../../config/urlconfigs'
import request from '../../api/request'
import { Amount } from 'config/config'

var bigDecimal = require('js-big-decimal')

const HoldersStatus: React.FC = (props: any) => {
  // All states for current screen
  const [holderStatusData, setHolderStatusData] = useState<any>({});

  useEffect(() => {
    holderStatus()
  }, [props.i18n.language])

  const getPretty = (val: any) => {
    var value = bigDecimal.getPrettyValue(val)
    return value
  }
  // get all BOA stats api
  const holderStatus = () => {
    request("GET", `${endpoints.allBoaStats}?currency=${props.i18n.language === "korean" ? "krw" : props.i18n.language === "chinese" ? "cny" : "usd"}`, {})
      .then((res: any) => {
        setHolderStatusData(res.data)
      })
      .catch((err: any) => {
        return err;
      });
  }

  return (
    <>

      <Row>
        <Col lg={12} md={12} sm={12}>
          <h2>BOA {props.i18n.t('Holders')}</h2>
        </Col>
      </Row>
      {/* BOA Holder Stats  */}
      <div className="holder-values">
        <Card>
          <div className="title">
            <ReactSVG src={price} />
            <p>{props.i18n.t('Price')}</p>
          </div>
          <div className="values">
            <h4>{props.i18n.t("ChangeCurrency")} {getPretty(holderStatusData.price)}</h4>
            {/* <p>Last 13s ago</p> */}
          </div>
        </Card>
        <Card>
          <div className="title">
            <ReactSVG src={frozen} />
            <p>{props.i18n.t('Frozen_Coin')}</p>
          </div>
          <div className="values">
            <h4>{holderStatusData.frozen_coin ? getPretty(Amount(holderStatusData.frozen_coin)) : getPretty(holderStatusData.frozen_coin)}</h4>
            <p>BOA</p>
          </div>
        </Card>
        <Card className="dc-res">
          <div className="title">
            <ReactSVG src={circulate} />
            <p>{props.i18n.t('Circulating_Supply')}</p>
          </div>
          <div className="values">
            <h4>{holderStatusData.circulating_supply ? getPretty(Amount(holderStatusData.circulating_supply)) : getPretty(holderStatusData.circulating_supply)}</h4>
            <p>BOA</p>
          </div>
        </Card>
        <Card>
          <div className="title">
            <ReactSVG src={rewards} />
            <p>{props.i18n.t('Received_Rewards')}</p>
          </div>
          <div className="values">
            <h4>{holderStatusData.total_reward ? getPretty(Amount(holderStatusData.total_reward)) : getPretty(holderStatusData.total_reward)}</h4>
            <p>BOA</p>
          </div>
        </Card>
      </div>
    </>

  )
}

export default withTranslation()(HoldersStatus)
