import React, { useEffect, useState } from 'react'
import { Container, Row, Col } from 'reactstrap'
import { useDispatch, useSelector } from 'react-redux'
import { PageHelmet, GenericSearchBar, TooltipComp } from 'components'
import {
  coinMarketCap,
  searchDataUpdater,
} from 'store/header/thunks'
import { GoArrowRight } from 'react-icons/go'
import { RouterPathEnum } from 'enums/RouterPathEnum'
import moment from 'moment'
import {useTranslation, withTranslation} from 'react-i18next'
import CopyAddressClipboard from 'components/CopyAddressClipboard/CopyAddressClipboard'
import Loader from 'components/Loader/Loader'
import { Amount } from 'config/config'
import request from '../../../api/request'
import endpoints from '../../../config/urlconfigs'
import {getPretty, getPriceOfFee} from 'utils/CalcUtils'

interface MarketCapStats {
  last_updated_at: any
  market_cap: number
  change_24h: number
  price: number
  time: any
  vol_24h: number
}

const TransactionOverview: React.FC = (props: any) => {
  // All states for current screen
  const dispatch = useDispatch()
  const [loading, setLoading] = useState(false);
  const [allData, setAllData] = useState<any>([]);
  const searchedData = useSelector((state: any) => state.header.searchedData);
  const hash = props.match.params['hash'];
  const marketCapStats: MarketCapStats = useSelector(
      (state: any) => state.header.marketCapStats,
  )
  const { i18n } = useTranslation();

  useEffect(()=>{
    dispatch(coinMarketCap(i18n.language))
  },[dispatch]);

  useEffect(()=>{
    console.log("");
    dispatch(coinMarketCap(i18n.language))
  },[i18n.language])


  const getTransactionData = (hash: string) => {
    request('GET', `${endpoints.transactionDetails}/${hash}`, {})
      .then((res: any) => {
        setAllData(res.data)
        setLoading(false)
      })
      .catch(() => {
        setLoading(false)
      })
  }

  // Get single Transaction details
  useEffect(() => {
    setLoading(true)
    if (props.location.state && props.location.state.utxoKey) {
      request('GET', `${endpoints.txhash}/${hash}`, {})
        .then((res: any) => {
          getTransactionData(res.data)
        })
        .catch(() => {
          setLoading(false)
        })
    }
    else {
      request('GET', `${endpoints.pendingTransactionOverview}/${hash}`, {})
        .then((res: any) => {
          if (res.data.length !== 0) {
            setAllData(res.data)
            setLoading(false)
          }
          else {
            getTransactionData(hash)
          }
        })
        .catch(() => {
          setLoading(false)
        })
    }
  }, [hash])

  const getOutputTotal = () => {
    let sum: any = 0
    if (allData.receivers) {
      for (let i = 0; i < allData.receivers.length; i++) {
        sum = sum + allData.receivers[i].amount
      }
      return parseFloat(Amount(sum)).toLocaleString('en-US', {
        maximumFractionDigits: 7,
        minimumFractionDigits: 7,
      })
    }
    else {
      return sum
    }
  }

  const getInputTotal = () => {
    let sum: any = 0
    if (allData.senders) {
      for (let i = 0; i < allData.senders.length; i++) {
        sum = sum + allData.senders[i].amount
      }
      return parseFloat(Amount(sum)).toLocaleString('en-US', {
        maximumFractionDigits: 7,
        minimumFractionDigits: 7,
      })
    }
    else {
      return sum
    }
  }

  const nodeDetails = (address: any) => {
    props.history.push(`${RouterPathEnum.NODEDETAILS}/${address}`)
  }

  const transactionOverview = (utxoKey: string) => {
    props.history.push(`${RouterPathEnum.TRANSACTIONOVERVIEW}/${utxoKey}`, { utxoKey: true })
  }

  return (
    <div className="page-container">
      <div id="transaction-overview">
        <PageHelmet
          title={`BOSAGORA Explorer - ${props.title}`}
          meta={{ name: 'Blocks', content: 'Lorem Ipsum' }}
        />
        <Container fluid="xl">
          <Row>
            <Col lg={12} md={12} sm={12}>
              {/* Transaction Overview Top section  */}
              <div className="top-header">
                <div className="left">
                </div>
                <div className="right">
                  <GenericSearchBar
                    history={props.history}
                    searchedDataGet={(searchid: any) =>
                      dispatch(searchDataUpdater(searchid))
                    }
                    searchedData={searchedData}
                  />
                </div>
              </div>
            </Col>
          </Row>
          <Row>
            <Col lg={12} md={12} sm={12}>
              <h2>{props.i18n.t('Transaction_Overview')}</h2>
            </Col>
          </Row>
          {loading ? (
            <div className="no_record">
              <Loader />
            </div>
          ) : allData.length === 0 ? (
            <div className="no_record">
              <h2>{props.i18n.t('NoRecord')}</h2>
            </div>
          ) : (
            <>
              {/* Transaction Overview Section  */}
              <div className="block-detail">
                <div className="block-hash">
                  <p>{props.i18n.t('Hash')}</p>
                  <div className="hash-copy copy-address">
                    {allData.tx_hash ? (
                      <>
                        <div className="address no_cursor">{allData.tx_hash}</div>
                        <CopyAddressClipboard
                          id={allData.tx_hash}
                          text={allData.tx_hash}
                        />
                      </>
                    ) : (
                      ''
                    )}
                  </div>
                </div>
              </div>
              {/* Input/Output Transaction Desktop view */}
              <div className="d-lg">
                <div className="io-container">
                  <div className="input">
                    <p>{props.i18n.t('Inputs')}</p>
                    <div className="link-color address_list">
                      {allData.senders === undefined || allData.senders.length === 0
                        ? props.i18n.t('No_Inputs')
                        : allData.senders.map((res: any, index: any) => (
                          <div key={index}>
                            <span onClick={() => nodeDetails(res.address)} key={index} className="address">
                              {res.address}
                            </span>
                            <p className="amount">
                              {props.i18n.t('Amount')}:{' '}
                              {parseFloat(Amount(res.amount)).toLocaleString(
                                'en-US',
                                {
                                  maximumFractionDigits: 7,
                                  minimumFractionDigits: 7,
                                },
                              )}{' '}
                              BOA
                            </p>
                          </div>
                        ))}
                    </div>
                    <div className="total-bal">
                      <span>{props.i18n.t('Total2')}:{' '}{getInputTotal()} BOA</span>
                    </div>
                    <div className="fee">
                      <p>
                        {props.i18n.t('Fee')}:{' '}
                        <span>
                          {allData.fee ? getPretty(Amount(allData.fee)) : getPretty(allData.fee)} BOA (
                          {props.i18n.t('ChangeCurrency')}{' '}
                          {getPriceOfFee(allData.fee,marketCapStats.price)})
                        </span>
                      </p>
                      <p className="fee">
                        {props.i18n.t('Size')}:&nbsp;
                        <span>{getPretty(allData.tx_size)} {props.i18n.t('Bytes')}</span>
                      </p>
                    </div>
                  </div>
                  <div className="transit">
                    <GoArrowRight />
                  </div>
                  <div className="output">
                    <div className="title-date">
                      <p>{props.i18n.t('Outputs')}</p>
                      <p>
                        {moment.utc(allData.time * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC
                      </p>
                    </div>
                    <div className="address_list">
                      {allData.receivers === undefined ||
                        allData.receivers.length === 0
                        ? props.i18n.t('No_Outputs')
                        : allData.receivers.map((receiver: any, index: number) => {
                          return (
                            <div key={index}>
                              <span onClick={() => nodeDetails(receiver.address)} className="link-color address">
                                {receiver.address}
                              </span>
                              <p className="amount">
                                {props.i18n.t('Amount')}:{' '}
                                {parseFloat(
                                  Amount(receiver.amount),
                                ).toLocaleString('en-US', {
                                  maximumFractionDigits: 7,
                                  minimumFractionDigits: 7,
                                })}{' '}
                                BOA
                              </p>
                            </div>
                          )
                        })}
                    </div>
                    <div className="total-bal">
                      <span>{props.i18n.t('Total2')}:{' '}{getOutputTotal()} BOA</span>
                    </div>
                  </div>
                </div>
              </div>
              {/* Input/Output Transaction Mobile Responsive view */}
              <div className="d-sm">
                <div className="more-detail">
                  <div className="item">
                    <p>{props.i18n.t('Date')}</p>
                    <div className="values">
                      {moment.utc(allData.time * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC
                    </div>
                  </div>
                  <div className="item">
                    <p>{props.i18n.t('Inputs')}</p>
                    <div className="values">
                      <div className="address_list">
                        {allData.senders === undefined ||
                          allData.senders.length === 0
                          ? props.i18n.t('No_Outputs')
                          : allData.senders.map((res: any, index: number) => {
                            return (
                              <div className="brdr" key={index}>
                                <span onClick={() => nodeDetails(res.address)} className="link-color address">
                                  {res.address}
                                </span>
                                <div className="amount">
                                  <p>{props.i18n.t('Amount')}:</p>
                                  <p>
                                    {parseFloat(
                                      Amount(res.amount),
                                    ).toLocaleString('en-US', {
                                      maximumFractionDigits: 7,
                                      minimumFractionDigits: 7,
                                    })}{' '}
                                    BOA
                                  </p>
                                </div>
                              </div>
                            )
                          })}
                      </div>
                      <div className="total-bal">
                        <span>{getInputTotal()} BOA</span>
                      </div>
                    </div>
                  </div>
                  <div className="item">
                    <p>{props.i18n.t('Outputs')}</p>
                    <div className="values">
                      <div className="address_list">
                        {allData.receivers === undefined ||
                          allData.receivers.length === 0
                          ? props.i18n.t('No_Outputs')
                          : allData.receivers.map(
                            (receiver: any, index: number) => {
                              return (
                                <div className="brdr" key={index}>
                                  <span onClick={() => nodeDetails(receiver.address)} className="link-color address">
                                    {receiver.address}
                                  </span>
                                  <div className="amount">
                                    <p>{props.i18n.t('Amount')}:</p>
                                    <p>
                                      {parseFloat(
                                        Amount(receiver.amount),
                                      ).toLocaleString('en-US', {
                                        maximumFractionDigits: 7,
                                        minimumFractionDigits: 7,
                                      })}{' '}
                                      BOA
                                    </p>
                                  </div>
                                </div>
                              )
                            },
                          )}
                      </div>
                      {/* <div className="brdr">
                    <span className="link-color address">
                      GYIEWGRFUYWEGIFGUYEW8SDGYFIEWGE7IRFWE8SDFfRERKWHERU
                    </span>
                    <p className="amount">
                      {props.i18n.t('Amount')}: 0.1234567890 BOA
                    </p>
                  </div> */}

                      {/* <span className="link-color address">
                    GYIEWGRFUYWEGIFGUYEW8SDGYFIEWGE7IRFWE8SDFfRERKWHERU
                  </span>
                  <p className="amount">
                    {props.i18n.t('Amount')}: 0.1234567890 BOA
                  </p> */}
                      <div className="total-bal">
                        <span>{getOutputTotal()} BOA</span>
                      </div>
                    </div>
                  </div>
                  <div className="item">
                    <p>{props.i18n.t('Fee')}</p>
                    <div className="values fees link-color">
                      <span>
                        {allData.fee ? getPretty(Amount(allData.fee)) : getPretty(allData.fee)} BOA
                      </span>
                      <b>
                        {getPretty(allData.tx_size)} {props.i18n.t('Bytes')}
                      </b>
                    </div>
                  </div>
                </div>
              </div>
              <h2>{props.i18n.t('Details')}</h2>
              <div className="more-detail">
                <div className="item">
                  <p>{props.i18n.t('Status')}</p>
                  <div className="values">{props.i18n.t(allData.status)}</div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Time')}</p>
                  <div className="values">
                    {moment.utc(allData.time * 1000).format('YYYY-MM-DD HH:mm:ss')} UTC
                  </div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Height')}</p>
                  <div className="values">{allData.height ? getPretty(allData.height) : allData.height}</div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Type')}</p>
                  <div className="values">{props.i18n.t(allData.tx_type)}</div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Lock_Height')}</p>
                  <div className="values">{getPretty(allData.lock_height)}</div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Total_Input')}</p>
                  <div className="values">{getInputTotal()} BOA</div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Total_Output')}</p>
                  <div className="values">{getOutputTotal()} BOA</div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Data_Fees')}</p>
                  <div className="values">
                    {allData.payload
                      ? allData.dataFee ? getPretty(Amount(allData.dataFee)) : getPretty(allData.dataFee)
                      : getPretty(allData.dataFee)}{' '}
                    BOA
                  </div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Transactions_Fees')}</p>
                  <div className="values">
                    {allData.fee ? getPretty(Amount(allData.fee)) : getPretty(allData.fee)} BOA
                  </div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Size')}</p>
                  <div className="values">
                    {getPretty(allData.tx_size)} {props.i18n.t('Bytes')}
                  </div>
                </div>
                <div className="item">
                  <p>{props.i18n.t('Payload')}</p>
                  <div className="values">
                    <div className="hash-copy copy-address link-color">
                      {allData.payload ? (
                        <>
                          <span className="address">{allData.payload}</span>
                          <CopyAddressClipboard
                            id="payload"
                            text={allData.payload}
                          />
                        </>
                      ) : (
                        ''
                      )}
                      {/* {(allData.payload === undefined  ||
                    allData.payload.length !== 0) && <ReactSVG src={copy} />} */}
                    </div>
                  </div>
                </div>
              </div>
              {/* Input Details section  */}
              <h2>{props.i18n.t('Inputs')}</h2>
              <div className="io-detail">
                {allData.senders === undefined || allData.senders.length === 0
                  ? props.i18n.t('No_Inputs')
                  : allData.senders.map((sender: any, index: any) => (
                    <Row key={index}>
                      <Col xl={8} lg={8} md={6} sm={12}>
                        <div className="item-list">
                          <div className="item">
                            <p>{props.i18n.t('Index')}</p>
                            <div className="values">{sender.index}</div>
                          </div>
                          <div className="item">
                            <p>{props.i18n.t('UTXO')}</p>
                            <div className="values">
                              {sender.utxo ?
                                <div className="copy-address">
                                  <div onClick={() => transactionOverview(sender.utxo)} className="link-color">
                                    {sender.utxo.slice(0, 6) +
                                      `…` +
                                      sender.utxo.slice(-1 * 6)}
                                  </div>
                                  <CopyAddressClipboard
                                    id={sender.utxo}
                                    text={sender.utxo}
                                  />
                                </div>
                                : ""}
                            </div>
                          </div>
                          <div className="item">
                            <p>{props.i18n.t('Signature')}</p>

                            <div className="values">
                              {sender.signature ?
                                <>
                                  <div className="copy-address">
                                    <div>
                                      {sender.signature.slice(0, 6) +
                                      `…` +
                                      sender.signature.slice(-1 * 6)}
                                    </div>
                                    <CopyAddressClipboard
                                        text={sender.signature}
                                    />
                                  </div>
                                </>
                              :null}
                            </div>
                          </div>
                          <div className="item">
                            <p>{props.i18n.t('Unlock_Age')}</p>
                            <div className="values">{props.i18n.t(sender.unlock_age)}</div>
                          </div>
                          <div className="item">
                            <p>{props.i18n.t('Bytes')}</p>
                            <div className="values">
                              <div className="long_values">
                                <TooltipComp
                                  id={index + '0'}
                                  limit={88}
                                  bytesText={Buffer.byteLength(sender.bytes)}
                                  text={sender.bytes}
                                />
                                <CopyAddressClipboard
                                  id={index + '1'}
                                  text={sender.bytes}
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </Col>
                      <Col xl={4} lg={4} md={6} sm={12}>
                        <div className="item-list">
                          <div className="item">
                            <p>{props.i18n.t('Address')}</p>
                            <div className="values">
                              <div onClick={() => nodeDetails(sender.address)} className="link-color">
                                {sender.address.slice(0, 6) +
                                  `…` +
                                  sender.address.slice(-1 * 6)}
                              </div>
                            </div>
                          </div>
                          <div className="item">
                            <p>{props.i18n.t('Amount')}</p>
                            <div className="values">
                              {getPretty(Amount(sender.amount))} BOA
                            </div>
                          </div>
                        </div>
                      </Col>
                    </Row>
                  ))}
              </div>
              {/* Output details section  */}
              <h2>{props.i18n.t('Outputs')}</h2>
              <div className="io-detail">
                {allData.receivers === undefined || allData.receivers.length === 0
                  ? props.i18n.t('No_Outputs')
                  : allData.receivers.map((reciever: any, index: number) => (
                    <Row key={index}>
                      <Col xl={8} lg={8} md={6} sm={12}>
                        <div className="item-list">
                          <div className="item">
                            <p>{props.i18n.t('Index')}</p>
                            <div className="values">{reciever.index}</div>
                          </div>
                          <div className="item">
                            <p>{props.i18n.t('UTXO')}</p>
                            <div className="values">
                              {reciever.utxo ?
                                <div className="copy-address">
                                  <div onClick={() => transactionOverview(reciever.utxo)} className="link-color">
                                    {reciever.utxo.slice(0, 6) +
                                      `…` +
                                      reciever.utxo.slice(-1 * 6)}
                                  </div>
                                  <CopyAddressClipboard
                                    id={reciever.utxo}
                                    text={reciever.utxo}
                                  />
                                </div>
                                : ""}
                            </div>
                          </div>
                          {/* <div className="item">
                          <p>LockType</p>
                          <div className="values">{reciever.lock_type}</div>
                        </div>
                        <div className="item">
                          <p>Bytes</p>
                          <div className="values">
                            <div className="address">{reciever.bytes}</div>
                          </div>
                        </div> */}
                          <div className="item">
                            <p>{props.i18n.t('LockType')}</p>
                            <div className="values">{props.i18n.t(reciever.lock_type)}</div>
                          </div>
                          <div className="item">
                            <p>{props.i18n.t('LockData')}</p>
                            <div className="values">
                              <div className="long_values">
                                <TooltipComp
                                  id={index + '2'}
                                  limit={44}
                                  bytesText={Buffer.byteLength(reciever.bytes)}
                                  text={reciever.bytes}
                                />
                                <CopyAddressClipboard
                                  id={index + '3'}
                                  text={reciever.bytes}
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </Col>
                      <Col xl={4} lg={4} md={6} sm={12}>
                        <div className="item-list">
                          {/* <div className="item">
                        <p>{props.i18n.t('Details')}</p>
                        <div className="values">
                          {props.i18n.t('Unspent')}
                        </div>
                      </div> */}
                          <div className="item">
                            <p>{props.i18n.t('Type')}</p>
                            <div className="values">
                              {
                                reciever.type === 0 ? props.i18n.t('Payment')
                                : reciever.type === 1 ? props.i18n.t('Freeze')
                                : reciever.type === 2 ? props.i18n.t('Coinbase')
                                : reciever.type
                              }
                            </div>
                          </div>
                          <div className="item">
                            <p>{props.i18n.t('Address')}</p>
                            <div className="values">
                              <div onClick={() => nodeDetails(reciever.address)} className="link-color">
                                {reciever.address.slice(0, 6) +
                                  `…` +
                                  reciever.address.slice(-1 * 6)}
                              </div>
                            </div>
                          </div>
                          <div className="item">
                            <p>{props.i18n.t('Amount')}</p>
                            <div className="values">
                              {getPretty(Amount(reciever.amount))} BOA
                            </div>
                          </div>
                        </div>
                      </Col>
                    </Row>
                  ))}
              </div>
            </>
          )}
        </Container>
      </div>
    </div>
  )
}

export default withTranslation()(TransactionOverview)
